.payment {
  padding: 0 0 3.375em;
  &__inner {
    position: relative;
  }
  &-bg {
    position: absolute;
    z-index: -1;
    top: -4.375em;
    right: -3.125em;
    width: 16.75em;
  }
  &-bg2 {
    position: absolute;
    z-index: -1;
    top: 1.125em;
    left: -3.125em;
    width: 6.1875em;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
  }
  &__card-group {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
    gap: 1.25em;
  }
  ._red {
    background-color: #e85222;
    padding: 1.5625em 2.1875em;
    .payment-card__title {
      color: #ffffff;
      margin-top: 0.9375em;
    }
  }

  ._gray {
    display: flex;
    flex-direction: column;
    background-color: #f7f7f7;
    .payment-card__title {
      margin-left: 0.75em;
    }
  }
}

.payment-card {
  border-radius: 0.3125em;
  &__top {
    height: 50%;
    display: flex;
    align-items: center;
    padding: 1.6875em 1.5625em;
  }
  &__title {
    span {
      font-size: 1.375em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.025em;
    }
  }
  &__icon {
    img {
      max-width: 3.625em;
      width: auto;
    }
  }
  &__bottom {
    height: 50%;
    display: flex;
    justify-content: space-between;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0.0625em;
      background: #d3d3d3;;
    }
  }
  &__bottom--2 {
   .payment-card__item {
     width: calc(100% / 3);
     flex-direction: column;
     justify-content: start;
     text-align: center;
   }
    .payment-card__item .payment-card__subtitle {
      margin: 0.625em 0 0 0;
    }
  }
  &__item {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.6875em 1.5625em;
    position: relative;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 0.0625em;
        height: 100%;
        background: #d3d3d3;
      }
    }
  }
  &__tooltip {
    position: absolute;
    top: 100%;
    right: 0;
    padding-top: 0.9375em;
    opacity: 0;
    pointer-events: none;
    &-wrap {
      width: 19.8125em;
      background-color: #ffffff;
      box-shadow: 0.25em 0.4375em 1.6875em 0 rgba(91,92,92,0.25);
      border-radius: 0.625em;
      padding: 1.5625em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: -2.1875em;
        right: 2.8125em;
        border: 1.5em solid transparent;
        border-bottom: 1.5em solid #ffffff;
        pointer-events: none;
      }
      p {
        font-size: 0.875em;
        text-align: start;
      }
    }
  }
  ._active {
    opacity: 1;
    pointer-events: all;
  }
  &__subtitle {
    margin-left: 0.75em;
    span {
      font-size: 1em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
}

@media screen and (max-width: 992px) {
  .payment {
    padding: 1em 0 2em;
    &-bg,
    &-bg2 {
      display: none;
    }
    &__grid {
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    ._red {
      padding: 1.25em 1em;
    }
  }
  .payment-card {
    &__top {
      height: auto;
      padding: 1.25em 1em;
    }
    &__bottom {
      height: auto;
      padding: 0 1em;
    }
    &__tooltip {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .payment {
    &__card-group {
      grid-template-columns: 100%;
    }
  }
  .payment-card {
    &__bottom {
      display: grid;
      grid-template-columns: 100%;
      gap: 2.1875em;
      padding: 1.25em 1em;
    }
    &__bottom--2 {
      .payment-card__item {
        flex-direction: row;
      }
      .payment-card__item .payment-card__subtitle {
        margin: 0 0 0 0.75em;
      }
    }
    &__item {
      width: 100% !important;
      justify-content: start;
      padding: 0;
      &:after {
        display: none;
      }
    }
  }
}