.employees {
  padding: 0 0 2.1875em;
  &__inner {
    background-color: #f5f5f5;
    border-radius: 0.625em;
    padding: 2.1875em;
    margin-top: 0.9375em;
    position: relative;
  }
  &-bg {
    position: absolute;
    z-index: -1;
    top: -4.1875em;
    right: -4.5625em;
    width: 12.25em;
  }
  &-bg2 {
    position: absolute;
    z-index: -1;
    top: 24.375em;
    left: -4.6875em;
    width: 6.25em;
  }
  &-bg3 {
    position: absolute;
    z-index: -1;
    top: 34.1875em;
    right: -3.125em;
    width: 7.6875em;
  }
  &-bg4 {
    position: absolute;
    z-index: -1;
    right: -5.9375em;
    bottom: 26.0625em;
    width: 7.6875em;
  }
  &-bg5 {
    position: absolute;
    z-index: -1;
    left: -4.375em;
    bottom: 14.875em;
    width: 7.6875em;
  }
  &__title {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 1.625em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.6875em;
      line-height: 140%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
      margin-left: 0.625em;
    }
  }
  &__group {
    &:not(:first-child) {
      margin-top: 1.375em;
    }
  }
  .white-bg {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 1.875em;
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 6.5625em / 4));
    gap: 2.1875em;
  }
  .mt {
    margin-top: 0.9375em;
  }
  &__subtitle {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      max-width: 4.125em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1.25em;
      line-height: 140%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
      margin-left: 0.9375em;
    }
  }
}

.employee-card {
  &__img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
    margin-top: 0.9375em;
    text-align: center;
  }
  &__name {
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
  }
  &__post {
    font-size: 1.125em;
    line-height: 120%;
    color: #7e7d7d;
  }
}

@media screen and (max-width: 992px) {
  .employees {
    padding: 2em 0;
    &__inner {
      padding: 1.25em;
    }
    &-bg,
    &-bg2,
    &-bg3,
    &-bg4,
    &-bg5 {
      display: none;
    }
    .white-bg {
      padding: 1.25em;
    }
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1em;
    }
  }
}

@media screen and (max-width: 576px) {
  .employees {
    &__grid {
      grid-template-columns: 100%;
    }
  }
}