.delivery{
  padding-bottom: 2.375em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 32em calc(100% - 36.625em);
    gap: 4.625em;
  }
}

.delivery-time{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.625em / 2));
  gap: .8125em;
  &__item{
    display: flex;
    flex-direction: column;
  }
  &__icon{
    width: 2.0625em;
    height: 2.0625em;
    margin: 0 auto;
    svg{
      width: 100%;
      height: 100%;
      fill: $primary;
    }
  }
  &__wrap{
    flex-grow: 1;
    margin-top: 1.5em;
    padding: 1.75em 1.5em 1em;
    background-color: $lightGrayBg;
    border-radius: .5em;
    text-align: center;
    position: relative;
  }
  &__text{
    font-size: 1em;
    line-height: 120%;
    font-weight: 700;
  }
  &__hint{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 0 .5em;
    text-align: center;
    @include center;
    height: 1.875em;
    border: 1px solid #e5e5e5;
    border-radius: .5em;
    white-space: nowrap;
    background-color: #fff;
    width: calc(100% - 2.5em);
    span{
      font-size: 1em;
      line-height: 120%;
      color: #878787;
    }
  }
}

.delivery-prices{
  margin-top: 2.25em;
}

.free-delivery{
  margin-top: 1em;
  padding: 1.25em 2em;
  border-radius: .5em;
  background-color: $lightGrayBg;
  position: relative;
  &__rects{
    position: absolute;
    z-index: -1;
    left: -3.1875em;
    top: 2.625em;
    width: 12.0625em;
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    align-items: center;
    letter-spacing: 0.025em;
    svg{
      width: 1.5em;
      height: 1.5em;
      flex-shrink: 0;
      fill: $primary;
      margin-right: .5em;
    }
  }
  &__hint{
    margin-top: 1.25em;
    letter-spacing: 0.025em;
    color: #7c7c7c;
    width: calc(100% + .5em);
  }
}

.pay-delivery{
  padding: 1.5em 0 .5em;
  margin-top: 1.375em;
  background-color: $lightGrayBg;
  border-radius: .5em;
  position: relative;
  &__rects{
    position: absolute;
    right: -4.4375em;
    bottom: 1em;
    z-index: -1;
    width: 7.625em;
  }
  &__top{
    padding: 0 2.1875em;
  }
  table{
    margin-top: 1.25em;
    border-collapse: collapse;
    width: 100%;
  }
  th,
  td{
    padding: 0 .5em;
    text-align: left;
    &:nth-child(1){
      padding-left: 2.1875em;
    }
    &:nth-child(2){
      text-align: right;
      padding-right: 2.1875em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  th{
    padding-bottom: .5em;
    font-weight: 700;
  }
  tr{
    &:nth-child(odd){
      td{
        background-color: #fff;
      }
    }
  }
  td{
    height: 3em;
  }
}

.delivery-map{
  svg{
    width: 100%;
    height: auto;
  }
  polygon,
  path{
    stroke: #949494;
    stroke-linecap: round;
    stroke-linejoin: round;
    &:hover{
      fill:$primary;
    }
  }
}

.map-point{
  position: fixed;
  @include center;
  transition: opacity .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
  }
  &__title{
    font-size: .875em;
    color: #ffffff;
  }
  &__hint{
    padding: .625em .75em;
    position: absolute;
    top: -3.75em;
    right: -3.75em;
    background-color: #fff;
    border-radius: .5em;
    box-shadow: .1875em .3125em 1.125em 0 rgba(91,92,92,0.43);
    span{
      font-size: .875em;
      line-height: 129%;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
  }

  .delivery-time{
    grid-template-columns: 100%;
  }

  .free-delivery,
  .pay-delivery{
    &__rects{
      display: none;
    }
  }

  .free-delivery{
    padding: 1em 1.25em;
  }

  .pay-delivery{
    &__top{
      padding-left: 1.25em;
      padding-right: 1.25em;
    }
    th,td{
      &:nth-child(1){
        padding-left: 1.25em;
      }
      &:last-child{
        padding-right: 1.25em;
      }
    }
  }

  .map-point{
    &__title{
      font-size: .625em;
    }
  }
}
