@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    svg{
        fill: #fff;
        width: 1.375em;
        max-height: 1.375em;
        margin-right: .6875em;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-black{
    background-color: $textBlack;
    color: #ffffff;
    &:hover{
        background-color: darken($textBlack,10%);
        color: #ffffff;
    }
}

.favourite-btn{
    &__icon{
        transition: all .3s;
        display: block;
        width: 1.4375em;
        height: 1.25em;
        background-image: url(../img/love.svg);
        @include bgImgSize(100%);
        &:hover{
            background-image: url(../img/favourite2.png);
        }
    }
    &._added{
        .favourite-btn__icon{
            background-image: url(../img/favourite3.png);
        }
    }
}

.text-link{
    color: $primary;
    text-decoration: underline;
    span{
        font-size: 1em;
        line-height: 150%;
        letter-spacing: 0.025em;
    }
    &:hover{
        text-decoration: none;
    }
}
