.services{
  padding: 1.5em 0 2.625em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.75em * 5 / 6));
    gap: 1.75em;
  }
}

.service-card{
  border-radius: .625em;
  background-color: $grayBg;
  padding: 1.375em 1.25em;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  &:hover{
    background-color: $primary;
    color: #fff;
    svg{
      fill: #fff;
    }
  }
  &__icon{
    max-width: 6.25em;
    height: 5.6875em;
    margin-bottom: 1em;
    svg{
      max-width: 100%;
      max-height: 100%;
      fill: $primary;
    }
  }
  .text15{
    letter-spacing: 0.025em;
  }
}

.po{
  padding: 2.25em 0 3.25em;
  background-color: $grayBg;
  overflow: hidden;
  .sectors__grid{
    @media screen and (min-width: 993px){
      grid-template-columns: 15.6875em 18.3125em 15.6875em;
      justify-content: center;
    }
  }
  &__rects{
    position: absolute;
    width: 7.5625em;
    top: 7em;
    left: -2.9375em;
  }
  &__rects2{
    position: absolute;
    bottom: 6.375em;
    right: -3.1875em;
  }
}

.services-1c{
  padding: 2.375em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5.75em / 3));
    gap: 2.875em;
  }
}

.service-card2{
  border-radius: .5em;
  background-color: $grayBg;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  &:hover{
    background-color: #fff;
    box-shadow: .3125em .5625em 2.5em 0 rgba(89,90,89,0.23);
  }
  &__img{
    display: block;
    @include imgCover(57.8%);
    img{
      border-radius: .5em .5em 0 0;
    }
  }
  &__content{
    flex-grow: 1;
    padding: 1em 1.25em 1.25em;
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-size: 1.25em;
    line-height: 135%;
    letter-spacing: 0.025em;
    text-align: center;
  }
  &__nav{
    margin-top: auto;
    padding-top: .75em;
  }
  &__link{
    width: 100%;
    height: 4.25em;
    border-radius: .5em;
    span{
      font-size: 1.125em;
    }
  }
}

.faq{
  padding: 2.625em 0;
  &__inner{
    position: relative;
  }
  .block-info{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.25em / 3));
    gap: 2.125em;
    margin-top: 2.25em;
  }
  &__nav{
    display: none;
  }
  &__nav2{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
    .m-btn{
      max-width: 100%;
      width: calc(100% / 3 - 4.25em / 3);
      height: 4.5625em;
      border-radius: .5em;
      img{
        width: 2.625em;
        margin-right: .75em;
        flex-shrink: 0;
      }
      span{
        font-size: 1.125em;
        line-height: 130%;
      }
    }
  }
  &__rects{
    position: absolute;
    top: .1em;
    left: -3em;
    z-index: -1;
    width: 16.75em;
  }
}

.search-field{
  width: 100%;
  .fg{
    input{
      font-size: 1.0625em;
      height: 4.23em;
      padding-left: 2em;
      padding-right: 4em;
      border-radius: .45em;
    }
  }
  .header-search__btn{
    width: 2em;
    right: 1.5em;
  }
}

.faq-card{
  position: relative;
  &._active,
  &:hover{
    cursor: pointer;
    .faq-card__title{
      color: #fff;
      background-color: $primary;
    }
    svg{
      fill: #fff;
    }
    .faq-card__tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__title{
    background-color: $grayBg;
    height: 100%;
    min-height: 6.25em;
    display: flex;
    align-items: center;
    padding: .5em 1.375em;
    border-radius: .5em;
    transition: all .3s;
    svg{
      width: 2.1875em;
      height: 2.1875em;
      margin-right: .75em;
      fill: $textBlack;
    }
    span{
      font-size: 1.0625em;
      line-height: 141%;
    }
  }
  &__tooltip{
    position: absolute;
    top: 50%;
    left: 99%;
    transform: translateY(-50%);
    padding-left: 1em;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
  }
  &__hint{
    padding: 1.5em 2em;
    width: 25em;
    background-color: #fff;
    border-radius: .5em;
    box-shadow: .125em .25em 1.125em 0 rgba(90,91,91,0.31);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      right: 100%;
      transform: translate(50%,-50%) rotate(-45deg);
      background-color: #fff;
      border-radius: .25em;
      width: 1.5em;
      height: 1.5em;
    }
  }

  @media screen and (min-width: 993px){
    &__tooltip{
      transition: all .3s;
    }
    &:nth-child(3n){
      .faq-card__tooltip{
        left: unset;
        right: 99%;
        padding: 0 1em 0 0;
      }
      .faq-card__hint{
        &:before{
          right: unset;
          left: 100%;
          transform: translate(-50%,-50%) rotate(-45deg);
        }
      }
    }
  }
}

.faq--page{
  padding: 0 0 3em;
  .faq__rects{
    top: -4.375em;
    left: unset;
    right: -3.0625em;
  }
}

.news{
  padding: 3.25em 0 4em;
  background-color: $grayBg;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  &__inner{
    position: relative;
    z-index: 3;
  }
  &__rects{
    position: absolute;
    top: -3.75em;
    right: 16.875em;
    width: 87.875em;
    z-index: 2;
  }
}

.companies-slider,
.news-slider{
  position: relative;
  z-index: 2;
  .swiper{
    &-container{
      width: calc(100% + 2.6875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
      padding-right: 2.6875em;
      transition: all .3s;
    }
    &-pagination{
      top: 100%;
      left: 0;
      margin-top: 1.875em;
      &-bullet{
        background: $textBlack;
      }
    }
    @media screen and (min-width: 993px){
      &-slide{
        opacity: 0;
        &-active,
        &-next,
        &-next + .swiper-slide,
        &-next + .swiper-slide + .swiper-slide{
          opacity: 1;
        }
      }
    }
  }
}

.news-card{
  border-radius: .5em;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  height: 100%;
  &:hover{
    box-shadow: .3125em .5625em 2.5em 0 rgba(89,90,89,0.23);
  }
  &__img{
    display: block;
    @include imgCover(63.1%);
    img{
      border-radius: .5em .5em 0 0;
    }
  }
  &__content{
    padding: 1em 1.25em 1.25em;
    @include grid100gap(.5em)
  }
  &__title{
    font-size: 1.125em;
    line-height: 135%;
    letter-spacing: 0.025em;
    font-weight: 600;
  }
  &__nav{
    display: flex;
  }
}

.free-consult{
  padding: 2.75em 0 3.625em;
  background-image: url(../img/bg.jpg);
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    max-width: 61.75em;
    margin-left: auto;
  }
  &__top{
    @include grid100gap(.75em);
  }
  &__title{
    font-size: 2.5em;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.025em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__btn{
    padding: 0 4.5em;
    height: 4.625em;
    border-radius: .5em;
    span{
      font-size: 1.125em;
    }
  }
}

.certificates{
  position: relative;
  z-index: 3;
  padding: 2em 0 2.75em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  .block-info{
    text-align: center;
  }
  &__rects{
    position: absolute;
    bottom: -.6875em;
    right: -.875em;
  }
}

.certificate-card{
  &__img{
    display: block;
    @include imgCover(136.2%);
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background-image: url(../img/frame.webp);
      background-size: 100% 100%;
      z-index: 2;
    }
  }
}

.comparison{
  padding: 2em 0 3em;
  &__inner{
    position: relative;
  }
  &__rects{
    position: absolute;
    width: 18.4375em;
    right: -8.125em;
    top: 16.5em;
  }
  .block-info{
    text-align: center;
  }
  &__hint{
    margin-top: 1.25em;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 14.5em;
    &-item{
      color: #656565;
      @include lastM(0 1.25em 0 0);
      span{
        font-size: .875em;
        line-height: 171%;
      }
    }
  }
}

.comparison-table{
  position: relative;
  z-index: 2;
  table{
    font-size: 1em;
    width: 100%;
    border-spacing: 0;
  }
  th,
  td{
    font-size: 1em;
    border-right: 1px solid #d1d1d1;
    &:nth-child(1){
      width: 18.3125em;
      border-right: none;
      padding-right: 1.75em;
    }
    &:nth-child(2){
      border-left: 1px solid #d1d1d1;
    }
  }
  tbody tr{
    position: relative;
    td{
      position: relative;
      padding: .5em;
      height: 5.75em;
      background-color: $grayBg;
      border-bottom: 1px solid #d1d1d1;
      &:first-child{
        background-color: transparent;
        &:after,
        &:before{
          display: none;
        }
      }
      &:nth-child(2){
        &:before{
          border-radius: .625em 0 0 .625em;
        }
        &:last-child{
          &:before,
          &:after{
            border-radius: 0 .625em .625em 0;
          }
        }
        &:after{
          width: 50%;
          left: unset;
          right: 0;
        }
      }
      &:after,
      &:before{
        content: '';
        position: absolute;
        width: calc(100% + .625em);
        height: calc(100% + .625em);
        top: -.3125em;
        left: -.3125em;
        box-shadow: .125em .25em 2.1875em 0 rgba(90,91,91,0.11);
        background-color: #fff;
        opacity: 0;
        transition: all .3s;
        pointer-events: none;
      }
      &:after{
        box-shadow: unset;
        z-index: 2;
      }
    }
    @media screen and (min-width: 993px){
      &:hover{
        z-index: 2;
        td{
          background-color: transparent !important;
          &:not(:first-child){
            border: none !important;
          }
          .comparison-table__info{
            position: relative;
            z-index: 3;
          }
          &:after,
          &:before{
            opacity: 1;
          }
        }
      }
    }

    &:last-child{
      td{
        &:nth-child(1){
          border-bottom: none;
        }
        &:nth-child(2){
          border-bottom-left-radius: .625em;
        }
        &:last-child{
          border-bottom-right-radius: .625em;
        }
      }
    }
    &:hover{
      td{
        background-color: #fff;
      }
    }
  }
  &__head,
  &__head2{
    text-align: center;
    span{
      font-size: 1.0625em;
      letter-spacing: 0.025em;
    }
  }
  &__head{
    height: 3.5em;
    border-top: 1px solid #d1d1d1;
    background-color: $grayBg;
    font-weight: 700;
    &:nth-child(2){
      border-top-left-radius: .625em;
    }
    &:last-child{
      border-top-right-radius: .625em;
    }
  }
  &__head2{
    height: 4em;
    font-weight: 400;
    &:nth-child(1){
      text-align: right;
      font-weight: 700;
      padding-right: 1.75em;
      span{
        font-size: 1.125em;
      }
    }
  }
  &__hint{
    display: none;
  }
  &__info{
    @include grid100gap(.5em);
    text-align: center;
    &-icon{
      width: 2.3125em;
      margin: 0 auto;
      img{
        width: 100%;
        display: block;
      }
    }
    .dash{
      display: block;
      transform: translateY(-75%);
    }
    &-title{
      font-size: .8125em;
      line-height: 120%;
      .dash{
        font-size: 2.3em;
      }
    }
    &-title2{
      font-size: .9375em;
      line-height: 160%;
      .dash{
        font-size: 2em;
      }
    }
    ._small{
      font-size: .87em;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 120%;
    letter-spacing: 0.025em;
    text-align: right;
    hyphens: auto;
  }
}

.companies{
  overflow: hidden;
  padding: 2.5em 0 5em;
  background-color: $grayBg;
  position: relative;
  .block-info{
    text-align: center;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    @media screen and (min-width: 992px){
      &-slide{
        width: 20%;
      }
    }
    &-slide{

      padding-right: 1.25em;
      &-next+.swiper-slide+.swiper-slide+.swiper-slide{
        opacity: 1;
      }
    }

  }
  &__rects{
    position: absolute;
    left: 0;
    top: -1.6875em;
    width: 12.4375em;
  }
  &__rects2{
    position: absolute;
    bottom: -1.5em;
    right: 0;
    width: 16.75em;
  }
}

.company-card{
  padding: 1.375em 1.625em;
  background-color: #fff;
  border-radius: .5em;
  height: 6.875em;
  @include center;
  img{
    max-width: 100%;
    max-height: 100%;
    display: block;
  }
}

.business{
  padding: 3.5em 0 4.25em;
  background-image: url(../img/business.png);
  background-size: cover;
  background-position: center;
  .block-info{
    text-align: center;
    color: #fff;
  }
}

.info{
  border-top: 1px solid #b6b6b6;
  padding: 3em 0 4em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
  }
  &__title{
    font-weight: 700;
  }
  .text{
    margin-top: 1em;
    p{
      &:not(:first-child){
        margin-top: 1em;
      }
    }
  }
}

.maintenance{
  padding: 2.75em 0 6.875em;
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    position: relative;
    z-index: 2;
  }
  &__rects{
    position: absolute;
    left: -11.6875em;
    top: 1.75em;
    width: 14.8125em;
  }
  &__rects2{
    position: absolute;
    right: -4.375em;
    bottom: 7.1875em;
    width: 90.8125em;
  }
}

.maintenance-card{
  padding: 1.75em 2.25em;
  background-color: $grayBg;
  border-radius: .5em;
  &__items{
    margin-top: 1.125em;
    @include grid100gap(.5em);
  }
}

.maintenance-item{
  padding: .75em 7.5em .75em 1.125em;
  min-height: 3.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  border-radius: .5em;
  position: relative;
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 6.875em;
    border-radius: 0 .5em .5em 0;
    span{
      font-size: .9375em;
    }
  }
  &__desc{
    font-size: .875em;
    line-height: 150%;
  }
  &__price{
    font-size: 1.0625em;
    line-height: 150%;
    font-weight: 700;
    white-space: nowrap;
    margin-left: .5em;
  }
}



@media screen and (max-width: 992px){
  .services{
    padding: 1.25em 0 1.625em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - .75em * 2 / 3));
      gap: .75em;
    }

  }

  .po{
    .tabs__current,
    .tabs__dropdown{
      background-color: #fff;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .services-1c{
    padding: 1.5em 0;
    &__grid{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1.5em / 3));
      gap: .625em .75em;
    }
  }

  .service-card2{
    &__img{
      padding-bottom: 91%;
    }
    &__content{
      padding: .75em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
    &__nav{
      padding-top: .5em;
    }
    &__link{
      white-space: unset;
      text-align: center;
      padding: .75em;
      height: unset;
      min-height: 3.75em;
      span{
        font-size: 1em;
        line-height: 125%;
      }
    }
  }

  .faq{
    padding: 2.5em 0 1.6875em;
    &:not(._all){
      .faq-card:nth-child(3)~.faq-card{
        display: none;
      }
    }
    &._all{
      .faq__nav{
        display: none;
      }
    }
    &__rects{
      display: none;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .875em;
