        img{
          width: 2.25em;
          margin-right: .625em;
        }
        span{
          font-size: 1em;
        }
      }
    }
  }

  .faq-card{
    &__title{
      height: unset;
      min-height: 5.375em;
      svg{
        width: 1.875em;
        height: 1.875em;
        margin-right: .625em;
      }
      span{
        font-size: .9375em;
      }
    }
    &__tooltip{
      position: static;
      opacity: 1;
      pointer-events: unset;
      padding: 0;
      transform: unset;
      display: none;
    }
    &__hint{
      padding: .875em 1.125em .25em;
      width: 100%;
      background-color: transparent;
      box-shadow: none;
      &:before{
        display: none;
      }
    }
    .text{
      p{
        font-size: .875em;
      }
    }
  }

  .search-field{
    .header-search__btn{
      width: 1.5625em;
    }
    .fg{
      input{
        font-size: 1em;
        height: 4.5em;
        padding-left: 1.875em;
        padding-right: 3.5em;
        border-radius: .5em;
      }
    }
  }

  .faq--page{
    padding-top: 0;
  }

  .news{
    padding: 1.875em 0;
    &__rects{
      display: none;
    }
  }

  .news-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 20.625em;
        padding-right: 1.875em;
      }
    }
  }

  .free-consult{
    padding: 2.75em 0;
    text-align: center;
    &__inner{
      margin-right: auto;
    }
    &__title{
      font-size: 1.875em;
      line-height: 107%;
    }
    &__desc{
      font-size: 1em;
      line-height: 150%;
    }
    &__nav{
      margin-top: 1.25em;
      justify-content: center;
    }
    &__btn{
      width: 25em;
      padding: 0;
      max-width: 100%;
      height: 4em;
      span{
        font-size: 1em;
      }
    }
  }

  .certificates{
    padding: 1.375em 0 2em;
    &__rects{
      display: none;
    }
  }

  .comparison{
    padding: 2.25em 0 2.75em;
    .container{
      padding: 0 .9375em;
    }
    &__hint{
      margin-top: 1em;
      @include grid100gap(.25em);
      padding: 0;
    }
    &__rects{
      display: none;
    }
  }

  .comparison-table{
    margin-top: 1em;
    position: relative;
    &__scroll{
      width: calc(100% + 1.875em);
      margin-left: -.9375em;
      padding: 0 .9375em;
      overflow-x: auto;
      overflow-y: hidden;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      background-color: #ffffff;
      box-shadow: .25em 0 1.3125em 0 rgba(91,92,92,0.12);
      border-radius: .3125em;
      width: 12.5em;
      height: 100%;
      z-index: 2;
    }
    thead{
      tr:nth-child(1){
        th{
          &:nth-child(1)~.comparison-table__head{
            white-space: nowrap;
          }
        }
      }
    }
    &__head2{
      &:nth-child(1){
        span{
          font-size: 1em;
          line-height: 150%;
        }
      }
    }
    th,td{
      &:nth-child(1){
        position: sticky;
        left: 0;
        width: 11.5em;
        min-width: 11.5em;
        z-index: 2;
        padding-right: 0;
      }
      &:nth-child(2){
        padding-left: 2em !important;
      }
      &~th,td{
        padding: 0 1.625em;
        //z-index: -1;
        position: relative;
      }
    }
    &__hint{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      color: #a0a0a0;
      text-align: right;
      font-weight: 400;
      img{
        width: 1.75em;
        flex-shrink: 0;
        margin-left: .5em;
      }
      span{
        font-size: .8125em;
        line-height: 120%;
        letter-spacing: 0.025em;
      }
    }
    &__title{
      font-size: .9375em;
    }
  }

  .companies{
    padding: 1.75em 0 2.3125em;
    .swiper{
      &-container{
        width: calc(100% + .875em);
      }
      &-slide{
        padding: .875em .875em 0 0;
      }
      &-pagination{
        display: none;
      }
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .companies-slider{
    margin-top: -1.25em;
  }

  .business{
    padding: 1.875em 0;
    background-image: url(../img/business2.jpg);
  }

  .info{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .text{
      margin-top: .625em;
    }
  }

  .maintenance{
    padding: 1.875em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .services-1c,
  .services{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
  }

  .free-consult{
    background-image: url(../img/bg_mob.jpg);
  }

  .company-card{
    padding: .875em .75em;
    height: 4.875em;
  }

  .maintenance-card{
    padding: 1em;
  }

  .maintenance-item{
    padding: .5em 1em;
    @include grid100gap(.5em);
    &__btn{
      position: static;
      width: 100%;
      height: 3em;
      border-radius: .5em;
    }
    &__price{
      margin: 0;
    }
  }
}
