.return {
  padding: 0 0 2.1875em;
  &__title {
    font-size: 1.6875em;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
  }
  &__content {
    margin-top: 1.5625em;
    position: relative;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
    gap: 1.875em;
    position: relative;
    z-index: 2;
  }
  &-bg {
    position: absolute;
    top: -4.375em;
    right: -3.125em;
    width: 16.75em;
  }
}

.return-card {
  background-color: #f5f5f5;
  padding: 1.5625em 2.1875em;
  &__top {
    display: flex;
    align-items: center;
  }
  &__icon {
    flex-shrink: 0;
    width: 2.0625em;
    height: 2.0625em;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
    margin-left: 0.625em;
  }
  &__text {
    margin-top: 0.9375em;
    p {
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.025em;
      color: #434242;
      &:not(:first-child) {
        margin-top: 1.5625em;
      }
    }
  }
}

.guarantees {
  background-color: #f5f5f5;
  padding: 2.1875em 0;
  &__title {
    font-size: 1.6875em;
    line-height: 120%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
  }
  &__content {
    position: relative;
    margin-top: 1.5625em;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
    position: relative;
    z-index: 2;
  }
  &-bg {
    position: absolute;
    top: -1.5625em;
    right: -3.125em;
    width: 7.875em;
  }
  &-bg2 {
    position: absolute;
    top: 2.1875em;
    left: -5.9375em;
    width: 12.0625em;
  }
}

.guarantees-card {
  background-color: #ffffff;
  padding: 1.25em;
  text-align: center;
  &__title {
    span {
      font-size: 1.125em;
      line-height: 120%;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
  &__term {
    margin-top: 0.9375em;
    span {
      font-size: 5em;
      line-height: 90%;
      font-weight: 800;
      letter-spacing: 0.025em;
      color: #e85222
    }
    &2 {
      span:nth-child(1) {
        font-size: 2.25em;
      }
    }
  }
  &__info {
    margin-top: 0.9375em;
    span {
      font-size: 0.875em;
      line-height: 140%;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
}

.guarantees-info {
  padding: 2.1875em 0;
  &__title {
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    i {
      display: block;
      flex-shrink: 0;
      width: 2.1875em;
      height: 2.1875em;
      img {
        width: 100%;
        height: 100%;
      }
    }
    span {
      font-size: 1.375em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
      margin-left: 0.9375em;
    }
  }
}

.text {
  p {
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.025em;
    color: #434242;
    &:not(:first-child) {
      margin-top: 1.875em;
    }
  }
  &-nav {
    margin-top: 1.875em;
    &__title {
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.025em;
      color: #434242;
    }

  }
  ul {
    margin-top: 0.625em;
    li {
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.025em;
      color: #434242;
      padding-left: 0.9375em;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0.625em;
        left: 0;
        width: 0.3125em;
        height: 0.3125em;
        background: #e85222;
        border-radius: 50%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .return {
    &__items {
      grid-template-columns: 100%;
    }
    &-bg {
      display: none;
    }
  }

  .return-card {
    padding: 1.5625em 1em;
  }

  .guarantees {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    &-bg,
    &-bg2 {
      display: none;
    }
  }

  .guarantees-card {
    &__term {
      span {
        font-size: 2.5em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .guarantees {
    &__items {
      grid-template-columns: 100%;
    }
  }
}
