.page-top{
    padding: 1.25em 0 1.75em;
    .text32{
        letter-spacing: 0.025em;
    }
    .text16{
        margin-top: .5em;
    }
}


.breadcrumbs {
    margin-bottom: 1.25em;
    &__item {
        font-size: .9375em;
        line-height: 110%;
        position: relative;
        font-weight: 400;
        display: inline;
        color: #808080;
        &:not(:first-child) {
            &:before {
                content: '';
                display: inline-block;
                margin: 0 .45em 0 .3em;
            }
        }
    }
}
