.certificates2,
.requisites{
  &__title {
    font-size: 1.6875em;
    line-height: 140%;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #434242;
  }
}

.requisites {
  padding: 2.8125em 0;
  &__content {
    display: flex;
    margin-top: 2.1875em;
    position: relative;
  }
  &-bg {
    position: absolute;
    top: 9.625em;
    left: -6.0625em;
    width: 7.625em;
  }
  &__img {
    width: 25.5625em;
    position: relative;
    z-index: 2;
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-left: 2.1875em;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
  }
  &__item {
    &-title {
      font-size: 1.5em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
    }
    &-text {
      margin-top: 0.75em;
      p {
        font-size: 1em;
        line-height: 150%;
        color: #434242;
        &:not(:first-child) {
          margin-top: 0.4375em;
        }
      }
    }
  }
}

.certificates2{
  padding-bottom: 2.375em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.75em * 3 / 4));
    gap: 2.75em;
    margin-top: 1.25em;
    position: relative;
    z-index: 2;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
  &__btn{
    height: 4em;
    width: 16.625em;
    max-width: 100%;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
  }
  &__rects{
    position: absolute;
    right: -5.875em;
    bottom: 2.6875em;
  }
}

.companies2{
  background-color: $grayBg;
  .block-info{
    text-align: left;
  }
  .text30{
    font-size: 1.6875em;
  }
}

.what{
  padding-bottom: 2.625em;
  &__inner{
    padding: 2.875em 4em;
    background: $grayBg;
    border-radius: .625em;
    position: relative;
  }
  &__title{
    text-align: center;
  }
  &__info{
    margin-top: 3em;
    @include grid100gap(1.25em);
    text-align: center;
    letter-spacing: 0.025em;
    position: relative;
    z-index: 2;
  }
  .text20{
    line-height: 150%;
    padding: 0 1em;
  }
  .text16{
    line-height: 168%;
  }
  &__quote{
    position: absolute;
    width: 5.4375em;
  }
  &__quote1{
    left: 2.25em;
    top: 6.6875em;
  }
  &__quote2{
    right: 1.625em;
    bottom: 2.3125em;
  }
  &__rects{
    position: absolute;
    left: 5.1875em;
    top: 0;
    width: 12.0625em;
  }
  &__rects2{
    position: absolute;
    left: 100%;
    top: 5.1875em;
    width: 3.125em;
  }
  &__rects3{
    position: absolute;
    bottom: -1.5em;
    left: 27.5625em;
    width: 3.125em;
  }
}

.advantages2,
.ability{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
  }
}

.advantages2{
  padding: 2.625em 0;
  &__inner{
    position: relative;
  }
  &__text{
    line-height: 188%;
  }
  &__grid{
    margin-top: 2em;
    position: relative;
    z-index: 2;
  }
  &__rects{
    position: absolute;
    left: -5.9375em;
    bottom: 8.125em;
    width: 12.0625em;
  }
  &__rects2{
    position: absolute;
    right: -3.125em;
    bottom: 7.5em;
    width: 7.875em;
  }
}

.advantage-card2{
  padding: 1.5em 1.625em;
  border-radius: .5em;
  display: grid;
  grid-template-columns: 4.375em calc(100% - 5.25em);
  gap: .875em;
  align-items: flex-start;
  background: $lightGrayBg;
  letter-spacing: 0.025em;
  position: relative;
  &:hover{
    background: $primary;
    color: #fff;
    svg{
      fill: #fff;
    }
    .faq-card__tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .faq-card__tooltip{
    left: 50%;
    bottom: 100%;
    top: unset;
    transform: translate(-50%,.5em);
    padding: 0;
  }
  .faq-card__hint{
    width: 21.375em;
    padding: 1.25em 1.875em;
    &:before{
      width: 1em;
      height: 1em;
      top: 100%;
      left: 50%;
      transform: translate(-50%,-50%) rotate(-45deg);
    }
    p{
      font-size: .875em;
    }
  }
  &__icon{
    height: 4.6875em;
    svg{
      display: block;
      max-width: 100%;
      max-height: 100%;
      fill: $primary;
    }
  }
  &__content{
    @include grid100gap(.75em);
  }
}

.certificates3,
.ability{
  .text30{
    font-size: 1.6875em;
  }
}

.certificates3{
  background-color: $lightGrayBg;
  padding: 2.5em 0 5.125em;
  &__rects{
    position: absolute;
    right: -5.875em;
    bottom: -3.1875em;
    width: 7.625em;
  }
}

.ability{
  padding: 2.25em 0 2.5em;
  &__grid{
    margin-top: 1.5em;
  }
  &__btn{
    width: calc(100% / 3 - 4.375em / 3);
    margin: 1.6875em auto 0;
    height: 5.1875em;
    border-radius: .5em;
    background-color: $textBlack;
    color: #fff;
    position: relative;
    padding: 1.25em 1.25em 1.25em 5.75em;
    &-img{
      position: absolute;
      left: -.75em;
      top: 40%;
      transform: translateY(-50%);
      width: 6.375em;
    }
    &-desc{
      font-size: .875em;
      line-height: 157%;
    }
  }
}

.ability-card{
  padding: 1.25em;
  border-radius: .5em;
  display: grid;
  grid-template-columns: 5.3125em calc(100% - 5.8125em);
  gap: .5em;
  align-items: flex-start;
  background: $primary;
  color: #fff;
  letter-spacing: 0.025em;
  &__icon{
    height: 5em;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    @include grid100gap(.75em);
  }
}


@media screen and (max-width: 992px) {
  .requisites {
    &__content {
      display: block;
      text-align: center;
    }
    &-bg {
      display: none;
    }
    &__img {
      display: block;
      max-width: 18.75em;
      width: 100%;
      margin: 0 auto;
    }
    &__desc {
      margin: 2.1875em 0 0 0;
    }
  }

  .certificates2{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
    }
    &__rects{
      display: none;
    }
  }

  .what{
    padding-bottom: 1em;
    &__inner{
      padding: 1em;
    }
    &__info{
      margin-top: 1.5em;
      gap: 1em;
    }
    &__quote,
    &__rects,
    &__rects2,
    &__rects3{
      display: none;
    }
    .text20{
      padding: 0;
      font-size: 1.125em;
    }
  }

  .advantages2,
  .ability{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .advantages2{
    padding: 1em 0;
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .advantage-card2{
    padding: 1em 1.25em;
    grid-template-columns: 3em calc(100% - 3.5em);
    gap: .5em;
    &__icon{
      height: 3em;
    }
  }

  .certificates3{
    padding: 1.375em 0 2em;
    &__rects{
      display: none;
    }
  }

  .ability{
    padding: 1em 0;
    &__btn{
      width: 100%;
      padding: 1em 1.25em;
      &-img{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .certificates2{
    &__grid{
      grid-template-columns: 18.75em;
      justify-content: center;
      gap: 1.875em;
    }
  }

  .advantages2,
  .ability{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}
