.condition,
.test{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.625em * 3 / 4));
    gap: 1.625em;
  }
}

.test{
  padding-bottom: 2em;
}

.test-card{
  padding: 1.25em 1.5em;
  border-radius: .5em;
  background-color: $lightGrayBg;
  display: flex;
  align-items: center;
  transition: all .3s;
  &:hover{
    background-color: $primary;
    color: #fff;
    svg{
      fill: #fff;
    }
  }
  &__icon{
    width: 4.8em;
    height: 5.5em;
    @include center;
    margin-right: .75em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: 100%;
      //width: auto;
      fill: $primary
    }
  }
}

.condition{
  padding: 2em 0 2.75em;
  background-color: $lightGrayBg;
  .text30{
    font-size: 1.6875em;
    text-align: left;
  }
}

.condition-card{
  background-color: $textBlack;
  color: #fff;
  border-radius: .5em;
  padding: 1.125em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  letter-spacing: 0.025em;
  transition: all .3s;
  position: relative;
  &:hover{
    background-color: #fff;
    color: $textBlack;
    .faq-card__tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .faq-card__tooltip{
    padding: 0;
    left: 50%;
    top: 100%;
    transform: translate(-50%,0%);
  }
  .faq-card__hint{
    width: 21em;
    padding: 1.25em 1.875em;
    &:before{
      width: 1em;
      height: 1em;
      top: unset;
      bottom: 100%;
      left: 50%;
      transform: translate(-50%,50%) rotate(-45deg);
    }
    p{
      font-size: .875em;
    }
  }
  &__icon{
    height: 5.625em;
    @include center;
    svg{
      height: 100%;
      width: 100%;
      fill: $primary;
    }
  }
  &__content{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  &__title{
    font-size: 1.375em;
    line-height: 114%;
    font-weight: 700;
  }
}

.free-consult2{
  padding: 1.125em 0;
  background-color: $primary;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__info{
    display: flex;
    align-items: center;
    color: #fff;
    flex-grow: 1;
    img{
      width: 4.75em;
      display: block;
      flex-shrink: 0;
      margin-right: .875em;
    }
  }
  &__arrows{
    width: 3.375em;
    display: block;
    flex-shrink: 0;
    margin: 0 2.5em 0 1.5em;
  }
  .advantage-card{
    flex-shrink: 0;
    width: 19.625em;
    letter-spacing: 0.025em;
    height: 4.6875em;
    &__title{
      max-width: unset;
      line-height: 150%;
    }
    &__icon{
      max-height: unset;
      right: -.5em;
    }
  }
}

.equip2{
  padding: 2.25em 0 2.75em;
  background-color: $lightGrayBg;
  &__inner{
    position: relative;
  }
  .text30{
    font-size: 1.6875em;
    text-align: left;
  }
  &__grid{
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1em));
    gap: 1.25em;
  }
  &__rects{
    position: absolute;
    width: 90.875em;
    left: -6em;
    top: 8.375em;
  }
  &__btn{
    width: calc(20% - 1em);
    height: 4em;
    margin: 2em auto 0;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .condition,
  .test{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }

  .test{
    padding-bottom: 1em;
  }

  .condition{
    padding: 1em 0;
  }

  .free-consult2{
    padding: 1em 0;
    &__inner{
      flex-direction: column;
    }
    &__info{
      align-items: flex-start;
      img{
        width: 3em;
        margin-top: .5em;
      }
    }
    .text27{
      font-size: 1.25em;
    }
    &__arrows{
      display: none;
    }
    .advantage-card{
      margin: 1em 0 0;
    }
  }

  .equip2{
    padding: 1em 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .75em;
    }
    &__rects{
      display: none;
    }
    &__btn{
      margin-top: 1em;
      width: calc(100% / 3 - .5em);
    }
  }
}


@media screen and (max-width: 576px){
  .condition,
  .test{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }

  .equip2{
    padding: 1em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
    &__btn{
      width: 100%;
    }
  }
}
