.text48{
  font-size: 3em;
  line-height: 125%;
}

.text32{
  font-size: 2em;
  line-height: 150%;
}

.text30{
  font-size: 1.875em;
  line-height: 160%;
}

.text27{
  font-size: 1.6875em;
  line-height: 140%;
  letter-spacing: 0.025em;
}

.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text22{
  font-size: 1.375em;
  line-height: 130%;
}

.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text18{
  font-size: 1.125em;
  line-height: 130%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 147%;
}

.text14{
  font-size: .875em;
  line-height: 143%;
}

.text13{
  font-size: .8125em;
  line-height: 138%;
}

.text-bold{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.gray-text{
  color: $textGray;
}

.primary-text{
  color: $primary;
}


@media screen and (max-width: 992px){
  .text32,
  .text30{
    font-size: 1.6875em;
    line-height: 133%;
  }
}
