.reviews{
  padding-bottom: .75em;
  margin-top: -.75em;
  &__nav{
    display: flex;
    justify-content: center;
    padding: 0 1.875em;
  }
  &__btn{
    border-radius: .5em;
    width: 16.625em;
    height: 4em;
    max-width: 100%;
    span{
      font-size: 1em;
    }
  }
}

.review-card{
  overflow: hidden;
  &:nth-child(even){
    background-color: $lightGrayBg;
  }
  &__inner{
    padding: 2em 0;
    display: grid;
    grid-template-columns: 16.5em calc(100% - 16.5em - 1.75em);
    gap: 1.75em;
    position: relative;
  }
  &__top{
    @include grid100gap(1em);
  }
  &__info{
    margin-top: 2.25em;
    @include grid100gap(.5em);
  }
  &__rects{
    position: absolute;
    bottom: 0;
    right: 5.125em;
    width: 16.75em;
  }
  &__rects2{
    position: absolute;
    right: 2.375em;
    top: 0;
    width: 24em;
  }
}


@media screen and (max-width: 992px){
  .review-card{
    &__rects,
    &__rects2{
      display: none;
    }
    &__top{
      gap: .25em;
    }
    &__info{
      margin-top: 1.5em;
      gap: .25em;
    }
  }
}


@media screen and (max-width: 576px){
  .review-card{
    &__inner{
      grid-template-columns: 100%;
    }
    .certificate-card{
      max-width: 18.75em;
      margin: 0 auto;
    }
  }
}
