.mistake {
  padding: 5em 0 3.125em;
  &__inner {
    text-align: center;
    max-width: 59.125em;
    margin: 0 auto;
    position: relative;
  }
  &-bg2 {
    position: absolute;
    right: -3.125em;
    bottom: 1.5625em;
    width: 16.75em;
  }
  &__title {
    width: 37em;
    margin: 0 auto;
    position: relative;
    img:nth-child(1) {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: -1;
      top: -3.625em;
      right: 8.4375em;
      width: 8.8125em;
    }
  }
  &__text {
    margin-top: 1.875em;
    p {
      font-size: 1em;
      line-height: 140%;
      letter-spacing: 0.025em;
      color: #434242;
      &:not(:first-child) {
        margin-top: 1.875em;
      }
    }
  }
  .search-field{
    margin-top: 2.1875em;
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding: 2em 0;
    &-bg,
    &-bg2 {
      display: none;
    }
    &__title {
      width: 18.75em;
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    &__title {
      width: 100%;
    }
  }
}
