.quick-knowing{
  padding: .625em 0;
  &__inner{
    display: grid;
    grid-template-columns: 11.875em calc(100% - 13.75em);
    gap: 1.875em;
    padding: 1em;
    border-radius: .5em;
    background-color: $lightGrayBg;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 63.75em;
    letter-spacing: 0.025em;
    @include grid100gap(.5em);
  }
}

.main{
  padding: .75em 0 2.5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 32.125em) 30.5em;
    grid-template-rows: 18.75em 11em;
    gap: 1.75em 1.625em;
  }
}

.banner-slider{
  position: relative;
  @media screen and (min-width: 993px){
    &._big{
      grid-row: 1/3;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      opacity: 0;
      background-color: #fff;
      &-active{
        opacity: 1;
      }
    }
    &-pagination{
      left: 4.125em;
      bottom: 3.75em;
      display: flex;
      align-items: center;
      justify-content: unset;
      width: unset;

    }
  }
}

.banner{
  height: 100%;
  border-radius: .5em;
  overflow: hidden;
  &__img{
    display: block;
    background-color: #ffffff;
    border-radius: .5em;
    position: relative;
    z-index: 2;
    &:hover{
      opacity: .8;
    }
    img{
      border-radius: inherit;
      width: 100%;
      height: auto;
      min-height: 100%;
      object-fit: contain;
    }
  }
}

.master{
  padding: 1em 1em 1em 1.25em;
  background-color: $lightGrayBg;
  border-radius: .5em;
  display: grid;
  grid-template-columns: auto 9.125em;
  gap: .75em 1.875em;
  &__content{

  }
  &__top{
    display: grid;
    grid-template-columns: 4.75em calc(100% - 5.75em);
    gap: 1em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .5em;
    }
  }
  &__info{
    @include grid100gap(.5em);
  }
  &__status{
    display: flex;
    align-items: center;
    &-pin{
      width: .6875em;
      height: .6875em;
      border-radius: 50%;
      flex-shrink: 0;
      display: block;
      margin-right: .375em;
    }
  }
  &__text{
    margin-top: .75em;
  }
  &__btn{
    height: 9.125em;
    flex-direction: column;
    text-align: center;
    border-radius: .5em;
    img{
      width: 3.875em;
      margin-bottom: .625em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.about{
  background-color: $grayBg;
  overflow: hidden;
}

.counts,
.direction,
.sectors{
  padding: 2em 0;
  &__inner{
    position: relative;
  }
  &__grid{
    position: relative;
    z-index: 2;
  }
}

.counts{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
  }
  &__rects1{
    width: 12em;
    position: absolute;
    bottom: -4.5625em;
    left: -1.375em;
  }
  &__rects2{
    width: 12.25em;
    position: absolute;
    right: -6.25em;
    bottom: 3em;
  }
}

.block-info{
  z-index: 2;
  position: relative;
  text-align: center;
  letter-spacing: 0.025em;
  margin-bottom: 1.5em;
  @include grid100gap(.25em);
  a{
    text-decoration: underline;
    color: $primary;
  }
  .text16{
    p{
      line-height: inherit;
    }
  }
}

.count-card{
  border-radius: .5em;
  background-color: #ffffff;
  padding: 2.125em 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  &__num{
    font-weight: 800;
    color: $primary;
    margin-right: 1em;
    span{
      letter-spacing: 0.025em;
      font-size: 5.375em;
    }
  }
  &__title{
    font-size: 1.0625em;
    line-height: 147%;
    letter-spacing: 0.025em;
  }
}

.direction{
  .block-info__desc{
    max-width: 66.25em;
    margin: 0 auto;
  }
  &__grid{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: calc(100 + 2.6875em);
    margin-left: -1.375em;
  }
  &__rects1{
    position: absolute;
    right: -3.1875em;
    bottom: 13.75em;
    width: 12.25em;
  }
  &__rects2{
    position: absolute;
    left: 14.75em;
    bottom: 1.9375em;
    width: 7.5625em;
  }
  &__rects3{
    position: absolute;
    left: -3.0625em;
    bottom: 38.875em;
    width: 3.0625em;
  }
}

.direction-card{
  margin: 1.875em 1.34375em 0;
  padding: 1.875em 1.25em 1.5em;
  width: calc(25% - 2.6875em);
  background-color: #fff;
  border-radius: .5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: all .3s;
  &:hover{
    box-shadow: .3125em .5625em 2.5em 0 rgba(89,90,89,0.23);
  }
  &__icon{
    height: 6.25em;
    margin-bottom: 1em;
    img{
      height: 100%;
    }
  }
  .text18{
    letter-spacing: 0.025em;
    hyphens: auto;
  }
}

.sectors{
  padding: 2.5em 0 3.3125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1em));
    gap: 1em 1.25em;
  }
  &__rects{
    width: 7.625em;
    position: absolute;
    bottom: -1.25em;
    right: -3em;
  }
}

.sector-card{
  display: grid;
  grid-template-columns: 3.875em calc(100% - 4.625em);
  gap: .75em;
  align-items: center;
  padding: .5em 1em;
  background-color: #ffffff;
  min-height: 5.625em;
  border-radius: .5em;
  transition: all .3s;
  &._active,
  &:hover{
    background-color: $primary !important;
    color: #fff;
    cursor: pointer;
    svg{
      fill: #fff;
    }
  }
  &._active{
    pointer-events: none;
  }
  &__icon{
    width: 100%;
    height: 3.875em;
    flex-shrink: 0;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .text18{
    hyphens: auto;
  }
}

.equip{
  padding: 2.25em 0 1.5em;
  overflow: hidden;
  .sector-card{
    background-color: $grayBg;
  }
  &__inner{
    position: relative;
  }
  &__content{
    margin-top: 2.625em;
  }
  &__rects{
    position: absolute;
    width: 92.6875em;
    top: 6em;
    left: -6em;
  }
}

.product-slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.3125em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 20%;
      height: auto;
      padding-right: 1.3125em;
      transition: all .3s;
    }
    @media screen and (min-width: 993px){
      &-slide{
        opacity: 0;
        &-active,
        &-next,
        &-next + .swiper-slide,
        &-next + .swiper-slide + .swiper-slide,
        &-next + .swiper-slide + .swiper-slide + .swiper-slide{
          opacity: 1;
        }
      }
    }
  }
}

.product-card{
  background-color: #ffffff;
  box-shadow: .25em .4375em .8125em 0 rgba(90,91,91,0.17);
  padding: 2.375em 1em 1.25em;
  display: flex;
  flex-direction: column;
  height: 100%;
  border-radius: .5em;
  position: relative;
  transition: all .3s;
  &:hover{
    box-shadow: .25em .4375em 3.1875em 0 rgba(90,91,91,0.43);
  }
  &__buttons{
    position: absolute;
    top: 1em;
    right: 1em;
    display: flex;
    align-items: center;
  }
  &__comparison{
    width: 1.375em;
    height: 1.375em;
    @include center;
    margin-right: .75em;
    svg{
      max-width: 100%;
      max-height: 100%;
    }
    &:hover{
      svg{
        fill: $primary;
      }
    }
  }
  &__img{
    display: block;
    position: relative;
    padding-bottom: 90%;
    margin-bottom: .5em;
    img{
      max-height: 100%;
      max-width: 100%;
      @include centerAbsolute;
    }
  }
  &__title{
    font-size: .9375em;
    line-height: 140%;
    hyphens: auto;
  }
  &__rating{
    display: flex;
  }
  &__bottom{
    margin-top: auto;
    padding-top: .75em;
  }
  &__prices{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: .625em;
  }
  &__price{
    font-size: 1.5625em;
    font-weight: 700;
  }
  &__nav{
    margin-top: auto;
    padding-top: .75em;
  }
  &__btn{
    width: 100%;
    height: 3.25em;
    border-radius: .25em;
    span{
      font-size: 1em;
      line-height: 131%;
    }
  }
  &__badge{
    position: absolute;
    top: 0;
    left: 0;
    min-height: 2em;
    padding: .25em .75em;
    border-radius: .5em 0 .5em 0;
    background-color: $primary;
    color: #fff;
    display: flex;
    align-items: center;
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
  &__badge2{
    align-self: flex-start;
    padding: .25em .5em;
    min-height: 1.5em;
    background-color: $grayBg;
    border-radius: .625em;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: .625em;
    span{
      font-size: .6875em;
      line-height: 110%;
    }
  }
}



@media screen and (max-width: 992px){
  .quick-knowing{
    margin-top: .5em;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__img{
      width: 10.875em;
    }
  }

  .main{
    &__inner{
      grid-template-columns: 100%;
      grid-template-rows: unset;
      gap: 1.5em;
    }
    ._mob-hide{
      display: none;
    }
  }

  .banner-slider{
    .swiper{
      &-pagination{
        margin-top: 1em;
        position: static;
        justify-content: center;
        &-bullet{
          background-color: $textBlack;
        }
      }
    }
  }

  .master{
    grid-template-columns: 100%;
    gap: .75em;
    padding: 1em;
    &__top{
      grid-template-columns: 3.875em calc(100% - 4.875em);
    }
    &__btn{
      height: 3.25em;
      flex-direction: row;
      img{
        width: 2.0625em;
        margin: 0 .625em 0 0;
      }
    }
    &__text{
      .text16{
        font-size: .9375em;
      }
    }
  }

  .about{
    padding: 1em 0;
  }

  .counts{
    padding: 1em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__rects1,
    &__rects2{
      display: none;
    }
  }

  .count-card{
    padding: 1em;
    &__num{
      span{
        font-size: 4em;
      }
    }
  }

  .direction{
    padding: 1em 0;
    &__grid{
      margin: 1em 0 0;
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1.625em / 3));
      gap: .6875em .8125em;
    }
    &__rects1,
    &__rects2,
    &__rects3{
      display: none;
    }
  }

  .direction-card{
    width: 100%;
    margin: 0;
    padding: 1.25em .75em;
    &__icon{
      height: 4em;
      margin-bottom: .625em;
    }
    .text18{
      font-size: .9375em;
      line-height: 120%;
    }
  }

  .sectors{
    padding: 1em 0 2.5em;
    &__grid{
      display: grid;
      grid-template-columns: repeat(4,calc(25% - 2.4375em / 4));
      gap: .75em .8125em;
    }
    &__rects{
      display: none;
    }
  }

  .sector-card{
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: unset;
    padding: 1em;
    &__icon{
      margin-bottom: .5em;
    }
    .text18{
      font-size: 1em;
      line-height: 112.5%;
    }
  }

  .equip{
    padding: 1.25em 0;
    &__content{
      margin-top: 1.0625em;
    }
    &__rects{
      display: none;
    }
  }

  .product-slider{
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        width: calc(100% / 3);
        padding-right: .75em;
      }
      @media screen and (min-width: 577px){
        &-slide{
          opacity: 0;
          &-active,
          &-next,
          &-next + .swiper-slide{
            opacity: 1;
          }
        }
      }
    }
  }

  .product-card{
    padding: 2.5em .75em .875em;
    &__img{
      padding-bottom: 75%;
    }
    &__title{
      font-size: .875em;
      line-height: 128%;
    }
    &__bottom{
      padding-top: .5em;
    }
    &__prices{
      display: block;
    }
    &__price{
      font-size: 1.25em;
    }
    .cartcalc{
      margin-top: .5em;
      width: 100%;
    }
    &__nav{
      padding-top: .5em;
    }
    &__btn{
      height: 2.625em;
      span{
        font-size: .875em;
      }
      svg{
        display: none;
      }
    }
    &__badge{
      min-height: 1.5em;
      span{
        font-size: .625em;
      }
    }
    &__badge2{
      margin-bottom: .5em;
      span{
        font-size: .625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .counts{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .count-card{
    padding: 1em;
    flex-direction: column;
    text-align: center;
    &__num{
      margin: 0 0 .5em;
    }
    br{
      display: none;
    }
  }

  .sectors,
  .direction{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .8125em / 2));
    }
  }

  .product-slider{
    .swiper{
      &-slide{
        width: 50%;
        opacity: 0;
        &-active,
        &-next{
          opacity: 1;
        }
      }
    }
  }

  .product-card{
    &__badge{
      max-width: 50%;
    }
  }
}
