.politics {
  padding: 0 0 2.1875em;
  position: relative;
  &-bg {
    position: absolute;
    top: 16.0625em;
    right: 0;
    width: 7.625em;
  }
  &-bg2 {
    position: absolute;
    top: 48.4375em;
    left: 0;
    width: 6.0625em;
  }
  &-bg3 {
    position: absolute;
    top: 114.4375em;
    right: 0;
    width: 6em;
  }
  &-bg4 {
    position: absolute;
    top: 160.0625em;
    left: 0;
    width: 7.375em;
  }
  &-bg5 {
    position: absolute;
    right: 0;
    bottom: 151.8125em;
    width: 7.625em;
  }
  &-bg6 {
    position: absolute;
    left: 0;
    bottom: 123.5625em;
    width: 6.0625em;
  }
  &-bg7 {
    position: absolute;
    right: 0;
    bottom: 57.5625em;
    width: 6em;
  }
  &-bg8 {
    position: absolute;
    left: 0;
    bottom: 7.1875em;
    width: 7.375em;
  }
  &__title {
    margin-bottom: 1.25em;
    span {
      font-size: 2em;
      line-height: 140%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
  .text {
    p {
      line-height: 120%;
      letter-spacing: unset;
      color: #606060;
      &:not(:first-child) {
        margin-top: 1.25em;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .politics {
    &-bg,
    &-bg2,
    &-bg3,
    &-bg4,
    &-bg5,
    &-bg6,
    &-bg7,
    &-bg8 {
      display: none;
    }
    &__title {
      span {
        font-size: 1.6875em;
      }
    }
  }
}