.contacts,
.company-requisites{
  &__inner{
    position: relative;
  }
  &__items {
    z-index: 2;
    position: relative;
  }
}

.contacts{
  padding-bottom: 2.25em;
  &__inner{
    position: relative;
  }
  &__items{
    @include grid100gap(1.375em);
  }
  &__rects{
    position: absolute;
    top: 24.5em;
    left: -4.625em;
    width: 6.1875em;
  }
  &__rects2{
    position: absolute;
    top: -4.4375em;
    right: -3.0625em;
    width: 16.75em;
  }
}

.contact-card{
  display: grid;
  grid-template-columns: calc(100% - 41.75em) 40em;
  gap: 1.75em;
  &__content{
    padding: 1.5em 2.125em;
    background-color: $lightGrayBg;
    border-radius: .5em;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
  }
  &__icon{
    margin-right: .625em;
    flex-shrink: 0;
    width: 1.5625em;
    height: 1.5625em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{

  }
  &__work{
    @include grid100gap(.125em);
    &-item{
      display: flex;
    }
  }
  &__day,
  &__time{
    font-size: 1em;
    line-height: 150%;
  }
  &__day{
    width: 3.25em;
    flex-shrink: 0;
    margin-right: .25em;
    text-align: right;
    font-weight: 700;
  }
  &._big{
    .contact-card__day{
      width: 4.5em;
    }
  }
  &__map{
    height: 100%;
    min-height: 4em;
  }
}

.company-requisites{
  padding: 3.125em 0;
  background-color: $lightGrayBg;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 2.1875em;
  }
  &__rects,
  &__rects2{
    position: absolute;
    width: 7.625em;
  }
  &__rects{
    top: 5em;
    left: -3.1875em;
  }
  &__rects2{
    right: -4.375em;
    top: 3.5em;
  }
}

.requisite-card{
  padding: 1.875em 2.625em;
  background-color: #fff;
  border-radius: .5em;
  letter-spacing: 0.025em;
  display: flex;
  flex-direction: column;
  &__text{
    margin: 1em 0;
    font-size: 1em;
    line-height: 187%;
    & *{
      line-height: inherit;
    }
  }
  &__btn{
    margin-top: auto;
    width: 100%;
    height: 4.375em;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .company-requisites,
  .contacts{
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .contacts{
    padding-bottom: 1em;
  }

  .contact-card{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    &__content{
      padding: 1em;
    }
  }

  .company-requisites{
    padding: 1.5em 0;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .requisite-card{
    padding: 1em;
    &__text{
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .contact-card{
    grid-template-columns: 100%;
    &__map{
      min-height: 15em;
    }
  }

  .company-requisites{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
