.footer{
  background-color: $textBlack;
  padding: 2.625em 0 3.125em;
  color: #cfcfcf;
  a{
    &:hover{
      color: #fff;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: 14.625em 12.25em 13.75em calc(100% - 53.75em);
    gap: 1.75em 4.375em;
  }
  @media screen and (min-width: 993px){
    &__left{
      grid-row: 1/3;
    }
    ._about{
      grid-row: 1/3;
      grid-column: 2/3;
    }
    ._contacts{
      grid-column: 4/5;
    }
    ._additional{
      grid-column: 3/4;
      grid-row: 2/3;
    }
  }
  &__logo{
    display: block;
    width: 100%;
    margin-bottom: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin: 1.5em 0 1.25em;
    height: 3.625em;
    border-radius: .5em;
    span{
      font-size: 1em;
    }
  }
  ._view2{
    display: none;
  }
  &__conf{
    font-size: .875em;
    line-height: 171%;
    text-decoration: underline;
    letter-spacing: 0.025em;
  }
  &__by{
    margin-top: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    img{
      width: 100%;
      display: block;
      margin-bottom: .5em;
    }
    span{
      font-size: .875em;
      line-height: 129%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
  }
  &__links{
    margin-top: 1.5em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .5em);
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__info{
    margin-top: 1.5em;
    display: flex;
    justify-content: space-between;
  }
  &__days{
    font-size: 1em;
    line-height: 218%;
    max-width: 14em;
    p{
      line-height: inherit;
    }
  }
  &__contacts{
    &-item{
      display: flex;
      @include lastM(0 0 1em);
    }
    &-icon{
      height: 1.5em;
      width: 1.125em;
      flex-shrink: 0;
      margin-right: .625em;
      @include center;
      img{
        width: 100%;
        display: block;
      }
    }
    &-links{
      @include grid100gap(.5em);
    }
    &-link{
      font-size: 1em;
      line-height: 150%;
      white-space: nowrap;
    }
  }
  .master{
    background-color: #636363;
    color: #ffffff;
  }
}

.socials{
  display: flex;
  align-items: center;
  margin-top: 3.5em;
  &__item{
    margin-right: 1em;
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
    img{
      max-width: 2.25em;
      max-height: 1.5625em;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2em 1.25em;
      padding-bottom: 11.75em;
      position: relative;
    }
    &__left{
      align-items: center;
      grid-row: unset;
      grid-column: 1/-1;
      display: grid;
      grid-template-columns: 12.25em calc(100% - 12.25em - 1.125em);
      gap: 1.125em;
    }
    &__btn{
      margin: 0;
    }
    &__info{
      display: block;
    }
    &__conf{
      position: absolute;
      bottom: 5.875em;
      left: 0;
      width: 100%;
    }
    &__c{
      bottom: 8em;
      left: 0;
      position: absolute;
    }
    &__by{
      flex-direction: row;
      text-align: left;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      img{
        width: 10.6875em;
        margin: 0 .75em 0 0;
        flex-shrink: 0;
      }
    }
    .master{
      grid-column: 1/-1;
    }
    &__logo{
      margin: 0;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    &__title{
      font-size: 1.125em;
    }
    &__link{
      span{
        font-size: .9375em;
      }
    }
    &__days{
      font-size: .9375em;
      line-height: 140%;
      br{
        display: none;
      }
      p{
        @include lastM(0 0 .75em);
      }
    }
    &__contacts{
      margin-top: 1.5em;
    }
  }

  .socials{
    margin-top: 1.5em;
  }
}
