.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid $lightGrayBg;
    padding: 0 1em;
    height: 3.13em;
    width: 100%;
    font-size: .9375em;
    background-color: $lightGrayBg;
    border-radius: .33em;
    color: $textBlack;
    &::placeholder {
      color: $textBlack;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.125em;
    height: 1.125em;
    margin: 0 .3125em 0 0;
    svg{
      max-width: 100%;
      max-height: 100%;
      fill: #c3c3c3;
    }
    &:hover {
      cursor: pointer;
      svg{
        fill: $primary;
      }
      &~.rating-input__item {
        svg{
          fill: $primary;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  svg{
    fill: $primary;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: .875em;
      height: .875em;
      margin-right: .125em;
    }
  }
}
