.about-page{
  padding: 2em 0 2.625em;
  background-color: $lightGrayBg;
  &__inner{
    position: relative;
  }
  .block-info{
    text-align: left;
  }
  &__rects{
    position: absolute;
    left: -6em;
    bottom: 1em;
    width: 12.0625em;
  }
  &__rects2{
    position: absolute;
    right: -3.125em;
    bottom: .375em;
    width: 7.875em;
  }
}

.about-nav{
  display: grid;
  grid-template-columns: repeat(7,calc(100% / 7 - 3.75em / 7));
  gap: .625em;
  &__btn{
    height: 3.5em;
    @include center;
    text-align: center;
    background-color: $lightGrayBg;
    border-radius: .5em;
    color: $textBlack;
    padding: 0 .75em;
    &:hover{
      color: #fff;
      background-color: $primary;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.history{
  padding: 1.875em 0 7.5em;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  .block-info{
    text-align: left;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 4.375em / 3));
    gap: 8.4375em 2.1875em;
    position: relative;
    z-index: 2;
  }
  &__rects{
    position: absolute;
    top: 30.5em;
    left: -3.125em;
    width: 12.0625em;
  }
  &__rects2{
    right: -4.5625em;
    top: 27.875em;
    position: absolute;
    width: 6.1875em;
  }
  &__rects3{
    right: -6.125em;
    top: 74.3125em;
    position: absolute;
    width: 7.6875em;
  }
  .line{
    position: absolute;
    height: 1px;
    width: 100%;
    border-top: 1px dashed #838383;
    right: 0;
    z-index: 2;
    pointer-events: none;
  }
  .line1{
    top: 16.125em;
    width: calc(100% - 2.25em);
  }
  .line2{
    top: 21.4375em;
  }
  .line3{
    top: 39.125em;
  }
  .line4{
    top: 44.4375em;
  }
  .line5{
    top: 62.125em;
  }
  .line6{
    top: 67.4375em;
  }
  .line7{
    top: 85.125em;
  }
  .line8{
    top: 90.4375em;
  }
  .line9{
    top: 108.125em;
    width: calc(100% - (100% / 3 - 4.375em / 3) / 2);
    right: unset;
    left: 0;
  }
  .round{
    pointer-events: none;
    position: absolute;
    height: 5.3125em;
    width: 5.3125em;
    left: 100%;
    transform: translateX(-50%);
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px dashed #838383;
      z-index: 1;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 100%;
      background-color: #fff;
      z-index: 2;
    }
    &._type2{
      width: 17.6875em;
      height: 17.6875em;
      left: unset;
      right: 100%;
      transform: translateX(50%);
      &:after{
        left: unset;
        right: 0;
      }
    }
  }
  .round1{
    top: 16.125em;
  }
  .round2{
    top: 39.125em;
  }
  .round3{
    top: 62.125em;
  }
  .round4{
    top: 85.125em;
  }
  .round5{
    top: 21.4375em;
  }
  .round6{
    top: 44.4375em;
  }
  .round7{
    top: 67.4375em;
  }
  .round8{
    top: 90.4375em;
  }
  .arrows{
    position: absolute;
    width: 3.25em;
    transform: translateY(-50%);
    pointer-events: none;
  }
  .arrows1,
  .arrows2,
  .arrows3{
    top: 16.125em;
  }
  .arrows7,
  .arrows8,
  .arrows9{
    top: 39.125em;
  }
  .arrows13,
  .arrows14,
  .arrows15{
    top: 62.125em;
  }
  .arrows19,
  .arrows20,
  .arrows22{
    top: 85.125em;
  }
  .arrows25,
  .arrows26{
    top: 108.125em;
  }
  .arrows1,
  .arrows7,
  .arrows13,
  .arrows19,
  .arrows25{
    left: 18.25em;
  }
  .arrows2,
  .arrows8,
  .arrows14,
  .arrows20,
  .arrows26{
    left: 46.8125em;
  }
  .arrows3,
  .arrows9,
  .arrows15,
  .arrows21{
    right: 5.625em;
  }

  .arrows4,
  .arrows5,
  .arrows6{
    top: 21.4375em;
  }
  .arrows10,
  .arrows11,
  .arrows12{
    top: 44.4375em;
  }
  .arrows16,
  .arrows17,
  .arrows18{
    top: 67.4375em;
  }
  .arrows22,
  .arrows23,
  .arrows24{
    top: 90.4375em;
  }
  .arrows4,
  .arrows10,
  .arrows16,
  .arrows22{
    left: 9.6875em;
  }
  .arrows5,
  .arrows11,
  .arrows17,
  .arrows23{
    left: 37.5em;
  }
  .arrows6,
  .arrows12,
  .arrows18,
  .arrows24{
    right: 14.5em;
  }
  &__show-all{
    display: none;
  }
}

.history-card{
  height: 14.5625em;
  padding: 1.125em 1.75em;
  border-radius: .5em;
  background-color: $lightGrayBg;
  position: relative;
  letter-spacing: 0.025em;
  z-index: 3;
  &:before{
    content: '';
    position: absolute;
    left: 100%;
    top: 50%;
    width: 1.5em;
    height: 1.5em;
    transform: translate(-50%,-50%) rotate(-45deg);
    background-color: inherit;
    border-radius: .125em;
  }
  &:nth-child(3n){
    @media screen and (min-width: 993px){
      &:before{
        display: none;
      }
    }
  }
  &:nth-child(odd){
    background-color: $primary;
    color: #fff;
  }
  &._last{
    .history-card__inner{
      align-items: center;
      justify-content: center;
    }
    .history-card__point{
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__inner{
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  .text15{
    line-height: 133%;
  }
  &__text{
    margin-top: .5em;
  }
  &__point{
    position: absolute;
    top: 100%;
    left: 2.25em;
    margin-top: 1.25em;
    width: .6875em;
    height: .6875em;
    border-radius: 50%;
    background-color: $primary;
    box-shadow: 0 0 .625em 0 $primary;
  }
  &__year{
    position: absolute;
    left: 0;
    top: 100%;
    margin-top: 3em;
    font-weight: 800;
    color: $primary;
    span{
      font-size: 2.25em;
      letter-spacing: 0.025em;
    }
  }
  &__quote{
    font-style: italic;
    margin-top: auto;
    padding-top: .5em;
  }
}

.goals{
  padding: 2.75em 0 2.1875em;
  background-color: $lightGrayBg;
  &__inner{
    position: relative;
  }
  &__rects{
    position: absolute;
    right: 4.25em;
    top: 9.875em;
    width: 9.3125em;
  }
  .text30{
    text-align: center;
  }
  &__items,
  &__items2{
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__items{
    margin-top: -14.75em;
    padding: 0 9.375em;
  }
  &__items2{
    margin-top: .5em;
    padding: 0 3.5em;
  }
  &__logo{
    width: 18.8125em;
    margin-top: -1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.quality-goal,
.goal-item{
  svg{
    fill: $primary
  }
  &__inner{
    border-radius: 50%;
    width: 100%;
    height: 100%;
    @include center;
    flex-direction: column;
    text-align: center;
  }
}

.quality-goal{
  margin: 0 auto;
  width: 22.625em;
  height: 22.625em;
  border: 1.125em solid rgba(255,255,255,0.46);
  border-radius: 50%;
  position: relative;
  &:before{
    content: '';
    @include centerAbsolute;
    width: 12em;
    height: 12em;
    border: 2.5em solid #fff;
    border-radius: 50%;
  }
  &__inner{
    border: 2em solid #fff;
  }
  svg{
    width: 5.6875em;
    height: 5.1875em;
    margin-bottom: 1.125em;
    position: relative;
    z-index: 2;
  }
  &__title{
    text-align: center;
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.025em;
    width: 110%;
    position: relative;
    z-index: 2;
  }
}

.goal-item{
  width: 20.1875em;
  height: 20.1875em;
  border: 1.125em solid rgba(255,255,255,0.46);
  border-radius: 50%;
  &__inner{
    padding: 1em;
    background-color: #fff;
  }
  svg{
    height: 5em;
    width: 100%;
    margin-bottom: .625em;
  }
  &__title{
    font-size: 1em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.025em;
  }
}

.mission{
  overflow: hidden;
  &__inner{
    position: relative;
    padding: 3em 0 2.875em;
  }
  &__by{
    font-style: italic;
    @include center;
    &:before,
    &:after{
      content: '';
      display: block;
      flex-shrink: 0;
      width: 2.5em;
      height: .0625em;
      background-color: #b2b2b2;
      margin: 0 .625em;
    }
  }
  &__rects{
    top: -1.5em;
    left: 8.5em;
    width: 74.375em;
    position: absolute;
  }
  .what__quote1{
    top: 7.5em;
  }
  .what__quote2{
    bottom: 3.5em;
  }
}

.direction2{
  .direction-card:not(:hover){
    background-color: $lightGrayBg;
  }
}

.principles{
  padding: 3em 0 3.5em;
  background-color: $lightGrayBg;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 9.75em / 4));
    gap: 3.25em;
    position: relative;
    z-index: 2;
  }
  @media screen and (min-width: 993px){
    .principle-card:nth-child(4n){
      .faq-card__tooltip{
        left: unset;
        right: 100%;
        transform: translate(6.625em,2em);
      }
    }
  }
  &__rects{
    position: absolute;
    left: -5.6875em;
    top: 2.875em;
    width: 8.75em;
  }
  &__rects2{
    position: absolute;
    right: -3em;
    top: 2.875em;
    width: 6.25em;
  }
}

.principle-card{
  padding: 1.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #ffffff;
  transition: all .3s;
  border-radius: .5em;
  position: relative;
  &:hover{
    background-color: $primary;
    color: #fff;
    svg{
      fill: #fff;
    }
    .faq-card__tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .faq-card__tooltip{
    left: 100%;
    bottom: 100%;
    top: unset;
    transform: translate(-6.625em,2em);
  }
  .faq-card__hint{
    width: 20em;
    padding: 1.25em .5em 1.25em 1.5em;
    text-align: left;
    &:before{
      display: none;
    }
    p{
      font-size: .875em;
    }
  }
  &__icon{
    margin-bottom: .875em;
    svg{
      height: 5em;
      width: 100%;
      fill: $primary;
    }
  }
  &__text{
    font-size: 1.125em;
    line-height: 150%;
    letter-spacing: 0.025em;
    hyphens: auto;
  }
}

.gallery{
  padding: 2em 0 1.5em;
}

.gallery-slider{
  overflow: hidden;
  .swiper{
    &-slide{
      width: 25%;
    }
    &-nav{
      @media screen and (min-width: 993px){
        display: flex;
        justify-content: center;
        margin-top: .875em;
        .swiper-button{
          transform: unset;
          position: static;
          margin: 0;
        }
      }
    }
  }
}

.gallery-card{
  display: block;
  &:hover{
    .gallery-card__img:before{
      opacity: 0;
    }
  }
  &__img{
    @include imgCover(100%);
    &:before{
      content: '';
      position: absolute;
      inset: 0;
      background-color: $black;
      opacity: .5;
      z-index: 2;
      transition: all .3s;
    }
  }
}

.certificates4{
  padding: 1.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: 33.5em calc(100% - 36em);
    gap: 2.5em;
  }
  &__text{
    margin-top: .875em;
    text-align: center;
    letter-spacing: 0.025em;
    padding: 0 2em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.75em * 3 / 4));
    gap: 1em 1.75em;
    .certificate-card{
      cursor: pointer;
    }
  }
}

.certificates4-slider{
  .swiper{
    &-container{
      overflow: hidden;
    }
    @media screen and (min-width: 993px){
      &-wrapper{
        transform: unset !important;
        display: block;
      }
      &-slide{
        width: 100% !important;
        display: none;
        &._active{
          display: block;
        }
      }
      &-nav{
        display: none;
      }
    }
  }
}

.companies3{
  background-color: #fff;
  .company-card{
    border: 1px solid #cccccc;
  }
}

.requisites2{
  background-color: $lightGrayBg;
  @media screen and (min-width: 993px){
    .requisites__title{
      text-align: center;
      font-size: 1.875em;
    }
  }
}

.edu{
  padding: 2.25em 0 1.75em;
  .block-info{
    margin: 0;
    text-align: left;
  }
}

.unique{
  background-color: $primary;
  color: #fff;
  &__inner{
    padding: 1.5em 0;
    display: flex;
    align-items: center;
    min-height: 6.125em;
    letter-spacing: 0.025em;
  }
}

.edu-advantages{
  padding: 2.125em 0 2.5em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 1.75em 3em;
  }
  &__item{
    display: flex;
  }
  &__icon{
    width: 7.5em;
    flex-shrink: 0;
    margin-right: 1em;
    svg{
      width: 100%;
      height: 7.5em;
      fill: $primary;
    }
  }
  .text20{
    line-height: 150%;
    margin-bottom: .25em;
  }
}

.services2{
  background-color: $lightGrayBg;
  position: relative;
  overflow: hidden;
  &__inner{
    display: flex;
    position: relative;
    z-index: 2;
  }
  &__rects{
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
  }
}

.services-item{
  display: flex;
  overflow: hidden;
  width: 4em;
  transition: all .5s;
  &._active{
    flex-grow: 1;
  }
  &__hidden{
    flex-shrink: 0;
  }
  &__show{
    cursor: pointer;
    width: 4em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1em 1em 1.5em;
    letter-spacing: 0.025em;
    color: #ffffff;
  }
  &__icon{
    width: 2.25em;
    height: 2.25em;
    fill: #fff;
    margin-bottom: 1em;
  }
  &__title{
    span{
      font-size: 1.125em;
      line-height: 150%;
    }
    @media screen and (min-width: 993px){
      writing-mode: tb-rl;
      span{
        display: block;
        transform: rotate(-180deg);
      }
    }
  }
  &__hidden{
    padding: 2.375em;
    @media screen and (min-width: 993px){
      display: block !important;
    }
  }
  .text{
    margin-top: 1em;
    max-width: 44.375em;
    p{
      margin: 0 !important;
    }
  }
}

.yandex-reviews{
  padding: 2.5em 0 3em;
  .badge{
    max-width: 100%;
  }
}



@media screen and (max-width: 992px){
  .about-page{
    padding: 1em 0;
    overflow: hidden;
    background-color: transparent;
    .text30{
      text-align: center;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .about-nav{
    display: none;
  }

  .history{
    padding: 1em 0 1.6875em;
    .line,
    .arrows,
    .round,
    &__rects,
    &__rects2,
    &__rects3{
      display: none;
    }
    .text30{
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 4em;
    }
    &:not(._open-all){
      .history-card:nth-child(3)~.history-card{
        display: none;
      }
    }
    &._open-all{
      .history__show-all{
        display: none;
      }
    }
    &__show-all{
      display: flex;
      width: 100%;
      height: 3.6875em;
      border-radius: .5em;
      margin-top: 4.25em;
      span{
        font-size: 1em;
        letter-spacing: 0.025em;
      }
      img{
        width: 1.625em;
        margin-right: .625em;
      }
    }
  }

  .history-card{
    height: unset;
    padding: 1em 2em;
    text-align: center;
    min-height: 4em;
    &._last{
      &:before{
        display: none;
      }
    }
    &:before{
      left: 50%;
      top: 100%;
    }
    &__inner{
      height: unset;
      overflow: unset;
    }
    &__point{
      display: none;
    }
    &__year{
      margin-top: 1.25em;
      left: 50%;
      transform: translateX(-50%);
    }
    .text15{
      font-size: .875em;
    }
  }

  .goals{
    padding: 1.75em 0;
    &__rects{
      display: none;
    }
    &__items{
      padding: 0;
      margin-top: -.5em;
    }
    &__items2{
      padding: 0;
      margin-top: .625em;
    }
    &__logo{
      display: none;
    }
  }

  .mission{
    &__inner{
      padding: 1.875em 0 2em;
    }
    &__rects{
      display: none;
    }
    .text20{
      font-size: 1.125em;
    }
    .text16{
      font-size: .9375em;
    }
    .what__quote{
      display: block;
    }
    .what__quote1{
      top: 7em;
      left: 0;
    }
    .what__quote2{
      bottom: 1.875em;
    }
  }

  .principles{
    padding: 1.75em 0 2.25em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    &__rects,
    &__rects2{
      display: none;
    }
  }

  .principle-card{
    padding: .875em;
    &__icon{
      margin-bottom: .5em;
      svg{
        height: 3.5em;
      }
    }
    &__text{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .gallery{
    padding: 1em 0;
  }

  .gallery-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .certificates4{
    padding: 1em 0 1.75em;
    &__grid{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__items{
      display: none;
    }
  }

  .edu{
    padding-top: 1.5em;
  }

  .unique{
    .text32{
      font-size: 1.5em;
      text-align: center;
    }
  }

  .edu-advantages{
    padding: 2em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__icon{
      width: 6.25em;
      svg{
        height: 6.25em;
      }
    }
  }

  .services2{
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
    &__rects{
      display: none;
    }
  }

  .services-item{
    width: 100%;
    display: block;
    &._active{
      display: block;
    }
    &__show{
      width: 100%;
      flex-direction: row;
      justify-content: unset;
      padding: .5em 1.875em;
      min-height: 4.125em;
    }
    &__icon{
      margin: 0 .3125em 0 0;
      flex-shrink: 0;
    }
    &__title{
      span{
        font-size: .9375em;
      }
    }
    &__hidden{
      padding: 1.5em 1.875em;
      display: none;
    }
    .text27{
      font-size: 1.5em;
    }
    .text{
      margin-top: .5em;
      p{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .quality-goal{
    width: 22.5em;
    height: 22.5em;
  }

