.stocks {
  padding: 0 0 2.1875em;
  &__title {
    span {
      color: #e55021;
    }
  }
  &__content {
    margin-top: 1.5625em;
    position: relative;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 5.625em / 4));
    gap: 1.875em;
    position: relative;
    z-index: 2;
  }
  &-bg {
    position: absolute;
    top: -4.6875em;
    right: -3.125em;
    width: 16.75em;
  }
  &-bg2 {
    position: absolute;
    left: -3.125em;
    bottom: 3.125em;
    width: 12.0625em;
  }
}

.stocks-past {
  background-color: #f5f5f5;
  padding: 2.5em 0;
  &__content {
    margin-top: 1.5625em;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 5.625em / 4));
    gap: 1.875em;
    position: relative;
    z-index: 2;
  }
  .stocks-card {
    .stocks-card__desc {
      background: #ffffff;
    }
  }
}

.stocks-card {
  display: flex;
  flex-direction: column;
  &__top {
    overflow: hidden;
    position: relative;
  }
  &__img {
    height: 14.5em;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__numbers {
    padding: 0.5em 1.875em;
    position: absolute;
    left: 0;
    bottom: 1.25em;
    border-radius:0 1.8125em 1.8125em 0;
    background-color: #e85222;
  }
  &__price {
    display: flex;
    align-items: center;
    font-size: 1.5625em;
    line-height: 105%;
    font-weight: 700;
    color: #ffffff;
    span {
      font-size: 0.9375em;
      line-height: 120%;
      font-weight: 400;
      color: #fdbca8;
      text-decoration: line-through;
      margin-left: 0.625em;
    }
  }
  &__economy {
    font-size: 0.75em;
    line-height: 105%;
    color: #ffffff;
    margin-top: 0.625em;
  }
  &__desc {
    flex-grow: 1;
    background-color: #f5f5f5;
    padding: 1.25em 1.875em;
  }
  &__subtitle {
    span {
      font-size: 0.875em;
      line-height: 120%;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
  &__title {
    margin-top: 0.9375em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
      letter-spacing: 0.025em;
      color: #434242;
    }
  }
  &__text {
    margin-top: 0.9375em;
    p {
      font-size: 0.9375em;
      line-height: 140%;
      letter-spacing: 0.025em;
      color: #434242;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:4;
      -webkit-box-orient: vertical;
    }
  }
}

@media screen and (max-width: 992px) {
  .stocks {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    &-bg,
    &-bg2 {
      display: none;
    }
  }

  .stocks-card {
    &__desc {
      padding: 1.5625em 1em;
    }
  }

  .stocks-past {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .stocks {
    &__items {
      grid-template-columns: 100%;
    }
  }

  .stocks-past {
    &__items {
      grid-template-columns: 100%;
    }
  }
}