.header{
  padding: 3.125em 0 5.625em;
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
    padding: .625em 0;
  }
  &__logo{
    display: block;
    width: 12.8125em;
    flex-shrink: 0;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__rects{
    position: absolute;
    top: 0;
    width: 12em;
    display: block;
    &._left{
      left: 0;
    }
    &._right{
      right: 0;
    }
    img{
      width: 100%;
    }
  }
}

.menu{
  &__scroll{
    display: flex;
    align-items: center;
  }
}

.advantages{
  position: absolute;
  bottom: .625em;
  left: 0;
  width: 100%;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 3.75em / 5));
    gap: .9375em;
  }
}

.advantage-card{
  display: flex;
  align-items: center;
  min-height: 3.75em;
  background-color: $lightGrayBg;
  border-radius: .25em;
  padding: .25em 4.5em .25em 1.25em;
  position: relative;
  &:hover{
    background-color: $grayBg;
  }
  &__title{
    font-size: 1em;
    line-height: 112.5%;
    max-width: 7.5em;
  }
  &__icon{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    max-width: 5.625em;
    max-height: 4em;
  }
}

.nav{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $lightGrayBg;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__show,
  &__link{
    display: flex;
    align-items: center;
    height: 3.125em;
    color: $black;
    position: relative;
    transition: all .3s;
    &:before{
      content: '';
      position: absolute;
      height: .25em;
      width: calc(100% + 1.875em);
      background-color: $primary;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 0 0 1em 1em;
      transition: inherit;
      opacity: 0;
    }
    span{
      font-size: 1em;
      line-height: 120%;
      letter-spacing: 0.025em;
    }
    &:hover{
      color: $black;
      cursor: pointer;
      &:before{
        opacity: 1;
      }
    }
  }
  &__show{
    svg,
    img{
      width: 1.125em;
      height: auto;
      flex-shrink: 0;
      margin-left: .25em;
    }
  }
  &__item{
    position: relative;
    z-index: 10;
    .dropdown{
      padding-top: 0;
      top: 2.625em;
    }
    &:hover{
      .dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.dropdown{
  position: absolute;
  top: 0;
  left: -1.5em;
  padding-top: 2.625em;
  z-index: 3;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background-color: #ffffff;
    box-shadow: .0625em .1875em .8125em 0 rgba(0,0,0,0.2);
    border-radius: .3125em;
    padding: 1em 1.125em;
    white-space: nowrap;
  }
  &__link{
    color: #727171;
    @include lastM(0 0 .75em);
    span{
      font-size: .875em;
      line-height: 140%;
      letter-spacing: 0.025em;
    }
    svg,
    img{
      width: 1.1875em;
      height: auto;
      flex-shrink: 0;
    }
    &:hover{
      color: $black;
    }
  }
}

.header-action{
  margin-left: auto;
  display: flex;
  align-items: center;
  &__group{
    position: relative;
    margin-left: 1.0625em;
    &:hover{
      .dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  .dropdown{
    padding-top: 2.375em;
    left: unset;
    right: -.625em;
    text-align: right;
    &__inner{
      align-items: flex-end;
    }
    &__link{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      svg,
      img{
        margin-left: .5625em;
      }
    }
  }
  &__btn{
    @include center;
    height: 1.75em;
    position: relative;
    z-index: 4;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 1.75em;
      height: auto;
    }
  }
  ._pin{
    position: absolute;
    top: -.33em;
    right: -.67em;
  }
  @media screen and (min-width: 993px){
    ._desktop-hide{
      display: none;
    }
  }
}

._pin{
  min-width: 1.33em;
  height: 1.33em;
  padding: 0 .1em;
  font-size: .75em;
  @include center;
  color: #fff;
  background-color: $primary;
  border-radius: 50%;
}

.header-search{
  width: 23.125em;
  max-width: 100%;
  position: relative;
  .fg{
    input{
      padding-left: 1.53em;
      padding-right: 3.67em;
      height: 3.47em;
      &::placeholder{
        color: #bbbbbb;
      }
    }
  }
  &__btn{
    position: absolute;
    right: 1.4375em;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: 1.3125em;
    &:hover{
      path{
        fill: $primary;
      }
    }
    svg{
      width: 100%;
      height: auto;
    }
    path{
      fill: $textBlack;
    }
  }
}

.header-search2{
  display: none;
}

.header-city{
  margin-left: 1.375em;
  position: relative;
  &:hover{
    .dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .dropdown{
    left: 0;
    padding-top: 2.125em;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-weight: 300;
    position: relative;
    z-index: 4;
    &:hover{
      color: $primary;
      span{
        border-color: $primary;
      }
    }
    svg{
      margin-right: .3125em;
      width: 1em;
      height: auto;
    }
    path{
      fill: $primary;
    }
    span{
      transition: inherit;
      font-size: .875em;
      display: inline-block;
      padding: .13em 0;
      letter-spacing: 0.025em;
      border-bottom: .5px dotted $textBlack;
    }
  }
}

.header-contacts{
  margin-left: 1.75em;
  display: flex;
  align-items: center;
  text-align: right;
  &__item{
    @include lastM(0 1.25em 0 0);
  }
  &__link{
    white-space: nowrap;
  }
}

.burger{
  display: none;
}

.catalog-menu{
  padding: .9375em 0;
  background-color: $lightGrayBg;
  &__toggle{
    display: none;
  }
  &__grid{
    display: flex;
    position: relative;
  }
  &__group{
    flex-grow: 1;
    @include lastM(0 .5em 0 0);
    &._active{
      .catalog-dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__group._active .catalog-menu__show,
  &__show:hover{
    background-color: $primary;
    color: #ffffff;
    path{
      fill: #fff;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    padding: 0 1em;
    background-color: #ffffff;
    height: 2.6875em;
    transition: all .3s;
    border-radius: .3125em;
    color: $textBlack;
    span{
      font-size: 1em;
    }
    svg,
    img{
      width: 1.125em;
      height: auto;
      flex-shrink: 0;
      margin-left: .25em;
    }
  }
  @media screen and (min-width: 993px){
    &__mob-view{
      display: block !important;
    }
  }
}

.catalog-dropdown{
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  margin-top: .5em;
  background-color: #ffffff;
  box-shadow: .625em 1.0625em 2.875em 0 rgba(91,92,92,0.41);
  border-radius: .625em;
  padding: 2em 2.375em 2.5em;
  display: grid;
  grid-template-columns: 7em calc(100% - 10em);
  gap: 3em;
  overflow: hidden;
  transition: all .3s;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  &__icon{
    height: 7em;
    border-radius: .3125em;
    @include center;
    background-color: $primary;
    img{
      width: 4.375em;
      display: block;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.0625em));
    gap: 2.25em 2.75em;
  }
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 700;
    letter-spacing: 0.025em;
    max-width: 91%;
    @include lastM(0 0 .75em);
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include lastM(0 0 2.25em);
  }
  &__link{
    @include lastM(0 0 .5em);
    span{
      font-size: .9375em;
      line-height: 150%;
      letter-spacing: 0.025em;
    }
    &:hover{
      color: $black;
    }
  }
  &__rect1{
    position: absolute;
    left: 0;
    top: 9em;
    width: 9.1875em;
    display: block;
  }
  &__rect2{
    position: absolute;
    width: 4.625em;
    right: -1.625em;
    top: 8.125em;
  }
  @media screen and (min-width: 993px){
    &__close{
      display: none;
    }
    &._type2,
    &._type3{
      left: unset;
      .catalog-dropdown__grid{
        grid-template-columns: repeat(2,calc(50% - 1.375em));
      }
    }
    &._type2{
      width: 60.125em;
      .catalog-dropdown__rect2{
        top: -4.5625em;
      }
    }
    &._type3{
      width: 46.375em;
      .catalog-dropdown__rect2{
        right: 0;
        top: unset;
        bottom: -12.125em;
      }
      .catalog-dropdown__group{
        &:nth-child(3){
          grid-column: 1/-1;
        }
      }
    }
    &._type4{
      width: 36.9375em;
      left: unset;
      .catalog-dropdown__grid{
        grid-template-columns: 100%;
      }
      .catalog-dropdown__rect2{
        top: 4.875em;
      }
    }
    &._type5{
      left: 12.875em;
      width: 55em;
      .catalog-dropdown__grid{
        grid-template-columns: repeat(2,calc(50% - 1.375em));
      }
      .catalog-dropdown__rect2{
        top: 3.8125em;
      }
    }
    &._type6{
      left: unset;
      right: 0;
      width: 42em;
      .catalog-dropdown__grid{
        grid-template-columns: 100%;
      }
      .catalog-dropdown__rect2{
        right: -3.125em;
        top: 3em;
      }
    }
  }
}


@media screen and (max-width: 1780px){
  .header{
    &__rects{
      width: 7.25em;
      &._left{
      }
      &._right{
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 0;
    &__inner{
      padding: 0;
    }
    &__rects{
      display: none;
    }
    &__logo{
      width: 9.5em;
      margin: 0 .75em;
      padding-bottom: .375em;
    }
    &._open{
      .burger{
        background-image: url(../img/close-white.svg);
        background-size: 1em auto;
        background-color: transparent;
      }
      .menu{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-action{
    ._hide-mob{
      display: none;
    }
  }

  .menu,
  .catalog-dropdown{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(#181818,.96);
    width: 100%;
    height: $fullHeight;
    color: #ffffff;
    transform: translateX(-200%);
    transition: all .4s;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    padding: 3.5em 0 0;
    &__grid,
    &__scroll{
      flex-direction: column;
      align-items: flex-start;
      overflow-y: auto;
      height: 100%;
      padding: 0 1.875em 2.125em;
    }
  }

  .menu{
    .header-search{
      input{
        background-color: #474747;
        border-color: #474747;
        color: #fff;
        &::placeholder{
          color: #7d7d7d;
        }
      }
      &__btn{
        path{
          fill: #7d7d7d;
        }
      }
    }
    .container{
      padding: 0;
    }
  }

  .burger{
    display: flex;
    flex-shrink: 0;
    width: 2.375em;
    height: 2em;
    position: relative;
    z-index: 11;
    transition: all .0s;
    border-radius: .3125em;
    background-color: $lightGrayBg;
    background-image: url(../img/menu.svg);
    @include bgImgSize(1.5em);
  }

  .nav{
    background-color: transparent;
    position: static;
    margin-top: 1.25em;
    &__inner{
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
    }
    &__item{
      @include lastM(0 0 1.25em);
    }
    &__link,
    &__show{
      height: unset;
      color: #ffffff;
      &:before{
        display: none;
      }
      path{
        fill: #fff;
      }
      &:hover{
        color: $primary;
        path{
          fill: $primary;
        }
      }
    }
    .dropdown{
      position: static;
      pointer-events: unset;
      opacity: 1;
      &__inner{
        background-color: transparent;
        box-shadow: none;
        padding: 1em 0 0 .75em;
      }
      &__link{
        &:hover{
          color: $primary;
        }
      }
    }
  }

  .header-city{
    margin: 1.25em 0 0;
    &__btn{
      color: #fff;
      span{
        border-color: #fff;
      }
      &:hover{
        color: $primary;
        span{
          border-color: $primary;
        }
      }
    }
  }

  .header-contacts{
    margin: 1.5em 0 0;
    &__item{
      @include lastM(0 .75em 0 0);
    }
  }

  .advantages{
    position: static;
    color: $textBlack;
    margin-top: 1.25em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: 1em .75em;
    }
  }

  .advantage-card{
    padding-left: 1em;
    &__icon{
      right: -.25em;
    }
    &:last-child{
      grid-column: 1/-1;
      .advantage-card__title{
        max-width: 100%;
      }
    }
  }

  .header-search{
    width: 100%;
  }

  .header-search2{
    display: block;
  }

  .catalog-menu{
    margin-top: .75em;
    &__toggle{
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding: 0 1.625em;
      height: 3.3125em;
      border-radius: .3125em;
      span{
        font-size: .9375em;
        line-height: 120%;
        letter-spacing: 0.025em;
      }
      svg{
        width: 1.125em;
        height: auto;
        margin-left: .25em;
      }
    }
    &__mob-view{
      margin-top: 1em;
      display: none;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .5em .75em;
    }
    &__group{
      @include lastM(0);
      &._active{
        .catalog-dropdown{
          transform: translateX(0%);
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__show{
      width: 100%;
      padding: 0 .5em;
      justify-content: center;
      svg{
        display: none;
      }
      span{
        font-size: .875em;
      }
    }
  }

  .catalog-dropdown{
    display: block;
    margin: 0;
    border-radius: 0;
    z-index: 100;
    &__rect1,
    &__rect2,
    &__icon{
      display: none;
    }
    &__close{
      position: fixed;
      left: 1.875em;
      top: .75em;
      background-color: transparent;
      width: 2.375em;
      height: 2em;
      img{
        width: 1em;
      }
    }
    &__grid{
      display: flex;
      gap: unset;
    }
    &__group{
      @include lastM(0 0 2em);
    }
    &__title{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-menu{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
    }
    &__show{
      height: 3.3125em;
    }
  }
}
