@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.tabs{
  &__current{
    display: none;
  }
}

.cartcalc{
  width: 5.8125em;
  height: 2.5em;
  display: grid;
  grid-template-columns: 1.75em calc(100% - 3.5em) 1.75em;
  border: 1px solid $grayBg;
  border-radius: .5em;
  button{
    height: 100%;
    width: 100%;
    @include center;
    background-color: $grayBg;
    &:hover{
      background-color: $primary;
      color: #fff;
    }
    span{
      font-size: 1.125em;
      line-height: 117%;
    }
  }
  input{
    height: 100%;
    width: 100%;
    border: none;
    font-size: 1.125em;
    line-height: 117%;
    text-align: center;
  }
  .ccalc-minus{
    border-radius: .5em 0 0 .5em;
  }
  .ccalc-plus{
    border-radius: 0 .5em .5em 0;
  }
}

.swiper-nav{
  .swiper-button{
    width: 2.8125em;
    height: 2.4375em;
    background-color: $primary;
    opacity: 1;
    transform: translateY(-50%);
    margin: 0;
    svg{
      max-width: 1.25em;
      max-height: 1.25em;
      stroke: #fff;
      fill: none;
    }
    &-prev{
      border-radius: 50% 0 0 50%;
      left: unset;
      right: 100%;
    }
    &-next{
      border-radius: 0 50% 50% 0;
      right: unset;
      left: 100%;
    }
  }
}

.swiper-pagination-bullet{
  opacity: 1;
  background-color: #fff;
  width: .4375em;
  height: .4375em;
  border-radius: 50%;
  margin: 0 .5em !important;
  &-active{
    transform: scale(1.57);
  }
}


@media screen and (max-width: 992px){
  .tabs{
    position: relative;
    &._active{
      .tabs__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__current{
      padding: .5em 3em .5em 1.125em;
      display: grid;
      grid-template-columns: 3em calc(100% - 3.75em);
      align-items: center;
      gap: .75em;
      height: 4.5625em;
      border-radius: .5em;
      background-color: $grayBg;
      background-image: url(../img/arrow-bottom.svg);
      background-size: 1.0625em auto;
      background-position: right 1.25em center;
      background-repeat: no-repeat;
      margin-bottom: 1.0625em;
      .sector-card__icon{
        height: 3em;
      }
      br{
        display: none;
      }
    }
    &__dropdown{
      margin-top: .25em;
      position: absolute !important;
      top: 100%;
      left: 0;
      width: 100%;
      grid-template-columns: 100% !important;
      gap: 0 !important;
      overflow-y: auto;
      max-height: 18em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      border-radius: .5em;
      background-color: $grayBg;
      box-shadow: .25em .4375em .8125em 0 rgba(90,91,91,.17);
      .sector-card{
        min-height: unset;
        align-items: unset;
        text-align: left;
        padding: 1em 1.125em;
        border-radius: 0;
        &:not(:hover),
        &:not(._active){
          background-color: transparent;
        }
        &__icon{
          display: none;
        }
        br{
          display: none;
        }
      }
    }
  }


  .swiper-nav{
    display: flex;
    justify-content: center;
    margin-top: .875em;
    .swiper-button{
      transform: unset;
      position: static;
      margin: 0;
      width: 2.375em;
      height: 2.375em;
      svg{
        max-width: 1.125em;
        max-height: 1.125em;
      }
    }
  }
}
